.effektivitat{
    @apply w-full h-fit py-32 my-20 bg-gradient-to-b from-[#6FA8B3] to-[#0A385A];
}
.effektivitatHeading{
    @apply text-4xl lg:text-7xl mb-10 font-bold text-primary-white text-center ;
}

.effektivitatText{
    @apply text-xl leading-10 text-primary-white font-semibold w-11/12 lg:w-3/4 m-auto;
}