.wrapper{
    @apply h-screen bg-[#23435c] text-center;
    @apply flex flex-col gap-16 justify-center items-center;
}

.text{
    @apply w-7/12;
    @apply text-6xl font-bold text-primary-white;
}
.btn{
    @apply my-3 px-4 py-2 rounded-full;
    @apply bg-primary-teal text-primary-white font-bold;
}