.covers {
  background: url(../../resources/images/Blue-and-Orange-Wallpaper.jpg) fixed;
  @apply bg-cover bg-center bg-no-repeat;
}

.firstsectionWrapper {
  @apply container mx-auto px-44 md:px-20  sm:px-8 max-[700px]:px-14;
}

.placing {
  @apply pl-28 p-64 max-[750px]:py-56 px-14 max-[550px]:py-44 pl-0 px-8;
  @apply md:pl-0;
}

.firstText {
  @apply text-white font-sans font-bold text-7xl max-[550px]:text-5xl;
  /* @apply sm:ml-20;
  @apply md:ml-20;
  @apply lg:ml-20; */
}

.secondText {
  @apply flex text-white font-sans mt-5 text-2xl;
  /* @apply sm:ml-20;
  @apply md:ml-20;
  @apply lg:ml-20; */
}
.secondsectionWrapper {
  @apply bg-neutral-800  md:p-0 sm:pt-16;
  @apply sm:p-12;
  @apply md:p-12;
  @apply lg:p-12;
}
.wraps {
  @apply container mx-auto px-44 md:px-16  max-[850px]:px-8 py-24 max-[950px]:px-16;
}
.cardPosition {
  /* @apply bg-white p-16 rounded-3xl overflow-hidden relative px-28; */
  /* @apply block p-20  shadow-lg  max-w-4xl px-28 py-20 mt-8 mb-8; */
  @apply bg-white rounded-3xl overflow-hidden relative px-12 py-16 mb-6;
}
.cardTitle {
  /* @apply text-gray-900 text-7xl leading-tight font-bold mb-2; */
  @apply text-6xl  max-[450px]:text-4xl font-bold text-left 
    text-primary-dark py-2 mt-3;
}

.cardSubTitle {
  @apply text-gray-900 text-3xl max-[450px]:text-xl leading-tight font-semibold mb-2 text-left;
}

.cardText {
  @apply text-lg font-medium;
}
.cardButton {
  @apply outline outline-black px-6 py-2.5 mt-14 font-medium text-lg leading-tight shadow-md hover:shadow-lg md:max-w-2xl
    focus:shadow-lg  focus:ring-0 
    active:shadow-lg transition 
    duration-150 hover:bg-black hover:text-white 
    rounded-full ease-in-out;
}
.textSingleButton {
  @apply text-center flex w-11/12 justify-center font-bold text-4xl text-white font-sans mt-20 pt-10;
}
