.head{
    @apply flex justify-center;
}

.CardWrapper{
    @apply block p-6  max-w-6xl;
}

.MainTitle{
    
}

.CardHeader1{
    @apply text-7xl font-bold text-center 
    text-primary-dark py-10 px-6;  
}

.CardHeader2{
    @apply text-primary-dark text-base mb-4 text-xl font-medium;
}

.image{
    background-image: url(../../../../../resources/images/Angst.webp);
    @apply bg-cover bg-no-repeat bg-center rounded-xl space-x-4 shadow-lg;
    @apply w-full h-auto;
}


.HeaderButton{
    @apply bg-[#005C81] px-6 py-3 
    font-medium text-lg leading-tight
    text-primary-white
    rounded-full ease-in-out;
}


