.firstsectionWrapper {
    @apply bg-fixed h-max bg-cover p-20;
    /* @apply md:h-full md:w-max md:bg-fixed; */
}

.aboutOverlay {
    @apply backdrop-blur-sm bg-opacity-80 h-full p-5;
    @apply md:py-6 md:px-14;
    @apply lg:py-8 lg:px-32; 
}

.firstText{
    @apply flex font-semibold text-5xl mt-5 text-left;
    @apply lg:text-7xl lg:mt-20;
}

.secondText{
    @apply flex text-2xl text-left;
    @apply  lg:mt-5 text-2xl;
}

.textSingleButton{
    @apply text-left flex w-11/12 justify-center font-bold text-4xl font-sans mt-20 pt-10;
}

.heading{
    @apply text-3xl py-4 font-bold text-gray-800;
}

.link {
    @apply hover:text-teal-200 text-gray-800 font-medium;
}

.link:active{
    @apply text-teal-200;
}