.Lernen{
    @apply flex justify-center;
}

.HeaderWrapper{
    @apply block px-3  max-w-6xl w-[98%];
    @apply md:px-6;
}
.secondsectionWrapper{
    @apply bg-white ;
    
}
.CardHeader1{
    @apply text-5xl font-bold text-center 
    text-primary-dark py-12 px-6 mt-6;  
    @apply md:text-7xl;
}
.Card2{
    @apply block p-8 rounded-3xl shadow-lg bg-white  mb-10;
    @apply md:max-w-6xl md:w-[100%] md:p-16;
}

.Card2Header{
    @apply text-primary-white text-5xl leading-tight font-bold mb-2;
    @apply md:text-7xl; 
}

.Card2Text{
    @apply text-primary-white text-base mb-4 font-medium;
}
.Card2Text p {
    @apply mb-3 text-lg font-[500];
}
.spacer{
    @apply mb-10;
}
.cardTitle{
    @apply text-primary-white text-7xl leading-tight font-bold mb-4;
}
.cardSubTitle{
    @apply text-primary-white text-3xl leading-tight font-medium;
}
.cardText{
    @apply text-primary-white mt-8 text-xl font-medium;
}
.cardPosition{
    @apply rounded-3xl overflow-hidden relative p-8 mb-6;
    @apply md:px-8 md:py-20;
    
}
