.cardBox2 {
    @apply w-[23rem] my-4 shadow-lg rounded-3xl
}

.upperSection {
    @apply flex flex-row px-4 py-4
}

.iconDes {
    @apply w-1/5 h-20 bg-[url('../../../resources/images/logo01.webp')] bg-cover bg-center
}

.cSection {
    @apply flex flex-col pl-4 w-4/5 items-start
}

.cTitle {
    @apply font-bold text-sm whitespace-normal
}

.rowItems{
    @apply flex flex-row items-center mt-1
}

.rowDes{
    @apply text-sm ml-2
}

.dSection {
    @apply flex flex-row px-4 py-4
}


.circle {
    @apply bg-gray-200 flex items-center justify-center w-8 h-8 rounded-full
}