.strategien{
    @apply w-full mt-20;
}

.strategienHeader{
    @apply text-4xl lg:text-7xl text-center text-primary-dark font-bold mb-10;
}

.strategienBody{
    @apply text-xl font-semibold text-primary-dark w-11/12 lg:w-3/4 m-auto mb-10;
}

.StrategienMain{
    @apply flex lg:flex-row justify-center flex-wrap w-full;
}

.strategeinRed{
    @apply h-fit w-11/12 lg:pb-36  lg:mb-20 mb-10 lg:w-5/12 bg-[#A7345C] p-16 lg:mr-10 lg:m-0  m-auto rounded-3xl;
}



.strategeinRed h1{
    @apply lg:text-7xl text-6xl font-bold text-primary-white  mb-10;
}

.strategeinRed p{
    @apply text-primary-white text-lg font-semibold;
}



.strategeinTeal{
    @apply h-fit w-11/12 lg:w-5/12 bg-[#244052] p-16 mb-10 lg:m-0 m-auto lg:pb-60 rounded-3xl;
}

.strategeinTeal h1{
    @apply lg:text-7xl text-6xl font-bold text-primary-white mb-10;
}

.strategeinTeal p{
    @apply text-primary-white text-lg font-semibold;
}


.strategeinGreen{
    @apply h-screen mb-10 lg:w-5/12 w-11/12 bg-[#60857B] pb-28 p-16 lg:mr-10 lg:m-0 m-auto rounded-3xl;
}

.strategeinGreen h1{
    @apply text-6xl lg:text-7xl font-bold text-primary-white mb-10;
}

.strategeinGreen p{
    @apply text-primary-white text-lg font-semibold;
}


.strategeinYellow{
    @apply h-fit lg:h-screen lg:w-5/12 w-11/12 bg-[#C68333] pb-40 p-16 lg:pb-16 pb-10 lg:m-0 m-auto rounded-3xl;
}

.strategeinYellow h1{
    @apply text-6xl lg:text-7xl font-bold text-primary-white mb-10;
}

.strategeinYellow p{
    @apply text-primary-white text-lg font-semibold;
}

.strategeinYellow a{
    @apply text-primary-dark underline;
}