.cardWrapper {
    @apply bg-primary-white w-full h-fit xl:w-full lg:h-[600px] xl:h-full;
    @apply rounded-3xl py-20;
}
.cardLongWrapper {
    @apply bg-primary-white w-full h-fit xl:w-full lg:h-[800px] xl:h-full;
    @apply rounded-3xl py-20;
}
.innerCard {
    @apply w-10/12 lg:w-9/12 m-auto;
}
.cardHeader {
    @apply text-5xl md:text-7xl lg:text-6xl font-black w-3/4 pb-4;
}
.link{
    @apply text-primary-teal underline;
}
.listElement{
    @apply font-semibold;
}
.listLink{
    @apply underline hover:text-primary-teal;
}