.curve {
    overflow: hidden;
    @apply absolute w-full overflow-hidden top-0 left-0 leading-[0]; 
}

.curve svg {
    position: relative;
    display: block;
    width: calc(270% + 0.3px);
    height: 60px;
}

.curve .shapeFill {
    fill: #ffffff;
}

.bottomCurve {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
}

.bottomCurve svg {
    position: relative;
    display: block;
    width: calc(150% + 1.3px);
    height: 50px;
}

.bottomCurve .shapeFillBottom {
    fill: #ffffff;
}
