.Sogelingt {
  @apply w-full h-fit xl:px-36 py-28 md:px-24 sm:px-12;
  background-color: transparent;
  background-image: linear-gradient(180deg, #75afb8 0%, #043255 100%);
  opacity: 1;
}

.SogelingtWrapper {
  @apply w-3/4 h-fit m-auto md:m-auto sm:m-4;
}

.SogelingtHeader {
  @apply text-center text-5xl font-bold text-primary-white lg:text-7xl sm:text-4xl;
}
.wrapper {
  @apply h-fit w-screen bg-gradient-to-b from-[#73acb6] to-[#083759] py-32;
}
.heading {
  @apply text-center text-5xl md:text-7xl font-bold text-primary-white;
  @apply w-full md:w-4/5 m-auto;
}
.centeredContainer {
  @apply m-auto w-11/12 md:w-10/12 xl:w-7/12;
}
