.moreAboutFirstSectionWrapper {
    @apply flex flex-col 
}
.firstsectionWrapper {
    @apply bg-fixed bg-cover p-20;
    /* @apply md:h-full md:w-max md:bg-fixed;  h-max*/
}

.firstsectionWrapper {
    background-image: url(../../../resources/images/road.jpeg);
}

.motionCards {
    @apply flex grid lg:grid-cols-2 justify-center
}

.firstMotionCard {
    @apply flex justify-center
}

.secondMotionCard {
    @apply flex justify-center
}

.firstDiv{
    @apply text-white font-sans ml-20 p-20;
}

.firstText{
    @apply flex font-semibold justify-center text-5xl ;
    @apply lg:text-7xl ;
}

.secondText{
    @apply flex text-2xl;
    @apply  lg:mt-5 text-2xl;
}

.cardstyle{
    @apply block py-10 p-2 rounded-3xl shadow-lg bg-white max-w-lg;
    @apply lg:p-20 
    
}

.cardModulOne {
    @apply text-gray-900 text-7xl leading-tight font-bold mb-2
}

.cardFur {
    @apply text-gray-900 text-4xl leading-tight font-semibold mb-2;
}

.cardThema {
    @apply text-xl font-medium mb-4 mt-8;
}

.cardNetzwerk {
    @apply mt-2 text-xl font-medium
}

.secondsectionWrapper {
    @apply bg-black w-full p-20;
}

.buttonModule {
    @apply mt-2 lg:mt-8 outline  px-6 py-2.5 text-black font-medium text-lg leading-tight shadow-md hover:shadow-lg focus:bg-blue-700 focus:shadow-lg  focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 hover:bg-black hover:text-white rounded-full ease-in-out;
}

.textSingleButton{
    @apply text-center flex w-11/12 justify-center font-bold text-4xl text-white font-sans mt-20 pt-10;
}

.cardGuide {
    @apply flex justify-center  m-10;
}

@media (min-width:320px) and (max-width:1200px) {
    .moreAboutFirstSectionWrapper {
        display: flex;
        flex-direction: column;
        overflow: hidden;
    }
    .firstsectionWrapper {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        align-items: center;
        text-align: center;
        margin: 0 auto;
    }

    .firstText {
        display: flex;
        flex-direction: row;
        text-align: center;
        margin: 0px 7rem 0px 0px;
    }

    .secondText {
        display: flex;
        flex-direction: row;
        margin: 0px 7rem 0px 0px;
        padding: 3rem 0px 0px 0px;
    }
}

@media (min-width:320px) and (max-width:1200px) {
    .secondsectionWrapper {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }

    .cardstyle {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        text-align: center;
        align-items: center;
        margin: 1rem;
        padding: 3rem;
        border-radius: 4rem;
        width: 100%;
    }

    .cardGuide {
        display: flex;
        flex-direction: column;
    }
}

@media (min-width:320px) and (max-width:1200px) {
    .firstMotionCard {
        display: flex;
        flex-direction: column;
        width: 75%;
        border-radius: 4rem;
        margin: 0 auto;
    }

    .secondMotionCard {
        display: flex;
        flex-direction: column;
        width: 100%;
        border-radius: 4rem;
    }
}

@media (min-width:320px) and (max-width:1200px) {
    .cardGuide {
        display: flex;
        justify-content: center;
        margin-top:0px;
        width: 100%;
        flex-wrap: nowrap;
    }
    .textSingleButton {
        display: flex;
        flex-direction: row;
        text-align: center;
    }
}

