.companyForm {
    @apply w-screen h-fit xl:p-20 bg-gray-100;
}

.companyFormWrapper {
    @apply m-auto w-full md:w-9/12 lg:w-8/12 xl:w-1/2 flex  lg:min-w-[1000px] flex-col px-20;
}
.mainTitle{
    @apply text-4xl font-bold py-4;
}
.subHeading{
    @apply font-semibold py-4;
}

.formGroup{
    @apply flex flex-col py-2;
}
.formInput, .textarea{
    @apply rounded-2xl;
}
.formLabel, .formLabelreq{
    @apply text-primary-grey xl:text-sm text-xl;
}
.formInput{
    @apply  h-16 lg:h-10 bg-primary-white;
}

.formLabelreq{
    @apply after:content-['*'] after:ml-0.5 after:text-red-500;
}

.formInput:focus{
    @apply ring-blue-100;
}

.subText{
    @apply text-sm xl:text-xs text-gray-400 w-fit my-2;
}
.btn{
    @apply inline-flex items-center justify-center h-12 px-6 font-medium tracking-wide my-3;
    @apply text-white transition duration-200 bg-red-400 rounded-2xl hover:bg-red-300 focus:outline-none;
}
.closingText{
    @apply text-sm xl:text-xs text-black py-16;
}

.validationError{
    @apply invisible peer-invalid:visible text-sm text-red-700 font-light;
}