.lifeEasyCard{
    @apply bg-primary-white flex flex-col justify-between p-10 pb-16 rounded-2xl w-1/3 h-fit;
}
.lifeEasyCardIcon{
    @apply text-8xl text-primary-teal mb-5;
}
.lifeEasyCardBody{
    @apply flex flex-col justify-between ;
}

.lifeEasyCardBodyHead{
    @apply text-4xl font-bold mb-3;
} 

.lifeEasyCardBodymain{
    @apply font-bold text-primary-dark/50 text-left mb-3;
}