.fullyAutomatedCard {
    @apply flex flex-col justify-between;
    @apply w-full h-full lg:min-w-[30rem];
    @apply p-5 py-10 lg:px-10 xl:px-20 xl:py-12;
    @appply rounded-3xl bg-no-repeat bg-cover;
}

.fullyAutomatedCardTitle {
    @apply lg:w-full;
}
.fullyAutomatedCardTitle h1 {
    @apply text-4xl md:text-6xl lg:text-6xl text-primary-white font-bold mb-10;
}
.fullyAutomatedCardBody {
    @apply w-full;
}
.fullyAutomatedCardBody p {
    @apply text-primary-white self-start mb-10 font-medium;
}

.fullyAutomatedCardBottom {
    @apply flex flex-col justify-around h-1/3;
}
.CardBottomList {
    @apply flex flex-row   text-primary-white mb-2 font-semibold;
}
.bottomCardIcon {
    @apply h-6 w-6 rounded-full bg-primary-white mr-2 text-primary-green text-xl font-bold text-center flex justify-center;
}

.FullyAutomatedCardBottomButton {
    @apply px-5 border-primary-white border-4 rounded-3xl h-12 rounded-full text-primary-white hover:bg-primary-white hover:text-primary-dark font-semibold mt-5;
}

