.serioseQuellen{
    @apply w-11/12 lg:w-3/4 m-auto;
}

.serioseQuellen h1{
    @apply text-5xl text-center lg:text-7xl font-bold text-primary-dark mb-10;
}

.serioseQuellen p{
    @apply text-xl font-semibold text-primary-dark/80 mt-5;
}

.serioseQueelenList{
    @apply my-10 flex flex-col;
}

.serioseQueelenList div{
    @apply flex flex-row items-center mb-1 ;
}

.serioseIcon{
    @apply text-3xl text-primary-green font-bold mr-5;
}

.serioseQueelenList p{
    @apply mt-0 w-full ;
}