.StellenanzeigenCard{
    @apply h-[100vh] lg:w-[24vw] pb-10 w-11/12 rounded-2xl bg-primary-grey-lighter lg:mr-1 flex flex-col items-center mb-20 relative shadow-primary-grey shadow-md;
}

.backgroundTeal{
    @apply w-full h-24 bg-primary-teal flex flex-col rounded-t-2xl justify-center text-primary-white items-center ;
}

.backgroundTeal h3{
    @apply text-2xl font-bold;
}
.backgroundTeal p{
    @apply text-sm;
}
.stellenanzeigenPrice{
    @apply flex flex-col items-center justify-center;
}
.stellenanzeigenPrice .price{
    @apply text-7xl font-bold text-primary-green mb-0;
}
.StellenanzeigenList{
    @apply mt-10 mb-10 w-full ;
}

.StellenanzeigenList ul li{
    @apply flex flex-row justify-start mb-4 text-sm font-bold text-primary-dark;
}

.StellenanzeigenListIcon{
    @apply mx-4 text-lg;
}

.greenBadge{
    @apply w-fit h-fit px-3 py-2 left-0 bg-primary-green rounded-r-2xl flex justify-center items-center absolute top-5;
}
.greenBadge p{
    @apply text-primary-white font-bold text-sm;
}

.promotiondescrip{
    @apply text-xs text-center self-center px-10 mt-5 leading-5 font-semibold mb-0;
}