.socialButton {
    @apply outline-none h-11 w-11 cursor-pointer text-lg mr-3 my-2 rounded-full flex flex-row justify-center items-center hover:-translate-y-1.5 duration-500 ease-in-out;
}

.buttonWrapper {
    @apply flex flex-row justify-center items-center p-2.5 rounded-3xl shadow-md drop-shadow-lg;
    @apply md:px-4 md:py-3;
}

.aboutBtn {
    @apply bg-transparent font-semibold py-2 px-4 border-2 rounded-full transition-all duration-300 ease-in-out hover:shadow-md hover:drop-shadow-lg;
}

/* Slider Buttons and dots */
.btnSlide {
    @apply bg-gray-500 w-10 h-10 absolute;
    @apply justify-center flex items-center;
    @apply rounded-full;
    border: 1px solid rgba(34, 34, 34, 0.287);
    cursor: pointer;
}

.btnSlideImg {
    @apply w-6 h-6;
}
.prev {
    top: 50%;
    left: 20px;
    transform: translateY(-60%);
}
.next {
    top: 110%;
    right: 20px;
    transform: translateY(-60%);
}

.WhiteBorderBtn{
    @apply bg-transparent font-semibold py-2 px-4 border-2 rounded-full transition-all duration-300 ease-in-out hover:shadow-md hover:drop-shadow-lg;
}