.wrapper{
    @apply mt-16 pt-16 bg-[#056] text-white;
}

.heading{
    @apply text-3xl font-medium text-center mb-8;
    @apply md:text-4xl md:font-bold md:mb-12;
}

.itemsWrapper{
    @apply w-full font-semibold;
    @apply md:flex;
}

.items {
    @apply w-full px-8 text-sm;
    @apply md:w-1/2 md:pl-12 md:text-base;
}

.buttons{
    @apply mt-8 w-full flex justify-center;
    @apply md:mt-16;
}

.button{
    @apply px-3 mx-1 py-2 mb-16 text-sm font-medium rounded-3xl;
    @apply md:text-base md:font-bold;
}