.wrapper{
    @apply flex justify-center ;
}
iframe{ 
    @apply block justify-center; 
    @apply bg-cover bg-no-repeat bg-center rounded-3xl;
}
.spacer{
    @apply mb-10;
}

.MainTitle{
    @apply text-6xl font-bold text-center 
    text-primary-dark py-6 px-6 mb-6;
    @apply md:text-7xl;
}
.SecondTitle{
    @apply text-xl  mt-0 mb-6 text-center 
    text-primary-dark px-6;
    @apply md:text-xl;
}
.Card2{
    @apply block p-10 bg-white max-w-5xl mb-4;
}
.Card2Text{
    @apply text-primary-dark text-base mb-4 font-normal text-xl leading-normal ;
}

