.mainWrapper{
    @apply flex w-full justify-center py-1 sticky bottom-0 bg-white z-10;
}

.button{
    @apply flex flex-row whitespace-nowrap justify-center items-center shadow-xl m-1 font-semibold text-[15px] py-2 px-4 rounded-3xl w-fit;
    @apply md:m-4 md:px-8 md:text-[18px] font-sans;
    @apply lg:px-16;
    @apply xl:px-32;
}

.buttonIcon{
    @apply mr-1
}
