.wrapper {
    @apply bg-primary-teal w-screen py-1;
    @apply sticky top-0 z-20;
}
.container {
    @apply h-8 w-9/12 md:w-1/5 m-auto;
    @apply flex items-center gap-2 justify-around;
}
.linkElement {
    @apply w-fit min-w-[80px] md:min-w-[100px];
    @apply text-primary-white font-bold;
    @apply text-center;
}
.selected {
    @apply p-2 h-max lg:h-full w-fit text-black text-sm bg-white rounded-full lg:m-0 text-center font-black;
    @apply min-w-[80px] md:min-w-[100px];
}
