.quellen{
    @apply w-full mt-20;
}
.quellenWhite{
    @apply w-11/12 lg:w-10/12 m-auto bg-primary-white shadow-lg mb-10  h-fit p-12 lg:p-16 rounded-3xl rounded-t-3xl;
}
.quellenWhite h1{
    @apply text-4xl mb-5 lg:text-7xl font-bold;
}
.quellenWhite p{
    @apply text-xl mt-5 font-semibold text-primary-dark;
}
.quellenMain{
    @apply flex lg:flex-row flex-col justify-center;
}
.quellenRed{
    @apply h-fit w-11/12 lg:pb-28  lg:mb-20 mb-10 lg:w-5/12 bg-[#A7345C] p-16 lg:mr-10 lg:m-0  m-auto rounded-3xl;
}

.quellenRed h1{
    @apply lg:text-7xl text-6xl font-bold text-primary-white  mb-10;
}

.quellenRed p{
    @apply text-primary-white text-lg font-semibold;
}



.quellenTeal{
    @apply h-fit w-11/12 lg:w-5/12 bg-[#244052] p-16 mb-10 lg:m-0 m-auto  rounded-3xl;
}

.quellenTeal h1{
    @apply lg:text-7xl text-6xl font-bold text-primary-white mb-10;
}

.quellenTeal p{
    @apply text-primary-white text-lg font-semibold;
}
