/*left box*/
.leftContainer{
    @apply flex flex-row-reverse md:contents
}
.leftBox{
    @apply bg-white text-black col-start-1 col-end-5 p-4 rounded-2xl my-4 ml-auto shadow-md flex flex-col items-start md:items-end;
    position: relative;
}

.leftBox::before {
    content: '';
    position: absolute;
    top: 35px;
    right: -10px;
    width: 30px;
    height: 30px;
    background-color: #fff; 
    transform: rotate(45deg);
    z-index: -1;
}

.upSectionRight::before {
    content: '';
    position: absolute;
     top: 50px; 
    right: -68px;
    width: 30px;
    height: 30px;
    background-color: #fff; 
    transform: rotate(45deg);
    z-index: -1;
}

.downSectionLeft{
    @apply col-start-5 col-end-6 md:mx-auto relative mr-10 flex  justify-center
}

/*right box*/
.rightContainer{
   @apply flex md:contents
}
.rightBox{
    @apply bg-white text-black col-start-6 col-end-10 p-4 rounded-2xl my-4 mr-auto shadow-md flex flex-col items-start
}

.upSectionRight{
    @apply col-start-5 col-end-6 mr-10 md:mx-auto relative flex justify-center;   
}


/*common*/

.header{
    @apply font-semibold text-lg mb-1
}

.description{
    @apply leading-tight text-justify
}
.circle{
    @apply w-12 h-12 absolute top-1/2 -mt-3 rounded-full bg-white shadow flex justify-center items-center text-black
}
.line{
    @apply h-full w-1 bg-white pointer-events-none
}
.dateBox{
    @apply hidden md:flex items-center justify-center w-48 text-lg font-semibold
}
.date{
    @apply text-sm mb-4 block md:hidden
}
