/*styling .Stellenanzeigen section */

.Stellenanzeigen{
    @apply w-full my-20;
}

.Stellenanzeigen h1{
    @apply text-center lg:text-5xl text-3xl text-primary-dark font-bold my-10;
}
.stellenanzeigenTab{
    @apply flex flex-col justify-center items-center my-10;
}
.stellenanzeigenHeader{
    @apply text-xl text-primary-teal mb-2 hover:cursor-pointer font-semibold;
}

.preiseUnderline{
    @apply w-40 h-[3px] bg-primary-dark;
}
.stellenanzeigenCards{
    @apply flex flex-wrap flex-row w-full justify-center mb-10;
}

.StellenanzeigenList ul li {
    @apply flex flex-row justify-start mb-3 text-[5px]  text-primary-dark items-start flex-wrap
  }
  
  .StellenanzeigenListIcon {
    @apply mx-4 text-xl self-start ;
  }

/*styling .preiseImage section */

.preiseImage{
    @apply my-20;
}

/*styling the .promotionMarketing section */
.promotionMarketing{
    @apply w-full my-20;
}

.promotionMarketing .promotionHeader{
  @apply text-center lg:text-5xl text-3xl text-primary-dark font-bold my-10;
}

.promotionTab{
    @apply flex flex-row justify-center items-center;
}

.promotionTab .stellenanzeigenHeader{
    @apply text-lg text-center;
}

.promotionTab .preiseUnderline{
    @apply w-48;
}

.promotionTab .stellenanzeigenTab{
    @apply mr-5;
}

.promotionLeft{
    @apply w-full flex flex-row flex-wrap justify-around items-center;
}

.promotionRight{
    @apply w-full flex flex-row flex-wrap justify-around items-center;
}

/*Styling the beraten section*/

.beraten{
    @apply w-full my-20;
}
.beratenHeader{
    @apply text-center lg:text-5xl text-3xl text-primary-dark font-bold my-5;
}

.beratenMain{
    @apply w-10/12 flex flex-row  m-auto;
}

.beratenMainRight{
    @apply w-[60%] ml-10;
}
.beratenMainRight h2{
     @apply  lg:text-4xl text-3xl text-primary-dark font-bold my-5;
}
.beratenMainRight p{
    @apply text-lg font-[500] text-primary-dark mb-3;
}

.beratenQuestion .beratenCollapse{
    @apply text-primary-dark flex items-center font-bold mb-2 text-lg hover:cursor-pointer;
}
.beratenQuestion .beratenCollapseTeal{
    @apply text-primary-teal flex items-center font-bold mb-2 text-lg hover:cursor-pointer;
}
.beratenIcon{
    @apply text-xl font-semibold mr-2 text-primary-dark;
}

.beratenQuestion p{
    @apply text-sm;
}


/*styling .kostenlose section*/

.kostenlose{
    @apply w-full bg-primary-grey-lighter my-20 py-10 h-fit;
}

.kostenlose h1{
    @apply text-2xl lg:text-4xl text-primary-dark font-bold text-center mb-5;
}

.kostenlose p{
    @apply text-lg text-center text-primary-dark font-semibold ;
}

.kostenloseMain {
    @apply w-11/12 lg:w-3/4 grid grid-cols-4 justify-center m-auto;
}
