/* styling the techniken section */

.techniken{
    @apply w-full my-20;
}

.techniken h1{
    @apply text-5xl lg:text-7xl text-center font-bold text-primary-dark mb-12;
}

.techniken p{
    @apply text-xl font-semibold text-primary-dark w-10/12 mb-3 leading-7 m-auto;
}

.techniken p span{
    @apply text-primary-green;
}

.technikenMain{
    @apply w-full my-20 ;
}

.technikenMain h3{
    @apply text-center text-4xl text-primary-dark font-bold mb-5;
}

.technikenMainBracket{
    @apply text-lg font-normal text-center text-primary-dark;
}

.technikenCards{
    @apply w-10/12 m-auto my-20 flex flex-col lg:flex-row  flex-wrap;
}

.technikenCardRed{
    @apply w-11/12 lg:w-[40vw] rounded-3xl h-fit lg:h-[105vh] lg:mr-7 mb-10 bg-[#A73358] p-10 lg:p-16;
}

.technikenCardRed h1{
    @apply text-left text-primary-white mb-5;
}

.technikenCardRed p {
    @apply text-xl text-primary-white w-full mx-0 mb-2;
}

.technikenCardTeal{
    @apply w-11/12 lg:w-[40vw] rounded-3xl h-fit lg:h-[105vh] m-auto lg:mr-0 lg:ml-0 mb-10 bg-[#45636F] p-10 lg:p-16;
}

.technikenCardTeal h1{
    @apply text-left text-primary-white mb-5;
}

.technikenCardTeal p {
    @apply text-xl text-primary-white w-full mx-0 mb-2;
}

.technikenCardGreen{
    @apply w-11/12 lg:w-[40vw] rounded-3xl h-fit lg:h-[95vh] m-auto lg:mr-7 mb-10 bg-[#5D8379] p-10 lg:p-16;
}

.technikenCardGreen h1{
    @apply text-left text-primary-white mb-10;
}

.technikenCardGreen p {
    @apply text-xl text-primary-white w-full mx-0 mb-2;
}


.technikenCardYellow{
    @apply w-11/12 lg:w-[40vw] rounded-3xl h-fit lg:h-[95vh] m-auto lg:mr-0 mb-10 bg-[#C68432] p-10 lg:p-16;
}

.technikenCardYellow h1{
    @apply text-left text-primary-white mb-10;
}

.technikenCardYellow p {
    @apply text-xl text-primary-white w-full mx-0 mb-2;
}

/* styling prioritaten section */

.prioritaten{
    @apply w-full my-20 py-20 h-fit bg-gradient-to-b from-[#6FA8B3] to-[#0A385A]
} 

.prioritaten h1{
    @apply text-5xl lg:text-7xl text-center font-bold text-primary-white mb-10;
}

.prioritaten p{
    @apply text-xl font-semibold text-primary-white w-10/12 mb-3 leading-7 m-auto;
}

.prioritatenCard{
    @apply w-11/12 lg:w-10/12 bg-primary-white rounded-3xl h-fit m-auto my-10  flex flex-col lg:flex-row items-center p-10 lg:p-16 mb-10;
}
.prioritatenCardMain{
    @apply w-full lg:w-1/2 px-8;
}
.prioritatenCard .prioritatenCardMain h1{
    @apply text-primary-dark text-left;
}

.prioritatenCard .prioritatenCardMain p{
    @apply text-primary-dark mx-0 text-lg w-full;
}

.prioritatenCard img{
    @apply w-1/2 rounded-3xl;
}


.prioritatenCardSingle{
    @apply w-11/12 lg:w-10/12 bg-primary-white rounded-3xl h-fit m-auto my-10   p-10 lg:p-16 mb-10;
}
.prioritatenCardSingle .prioritatenCardMain{
    @apply w-full ;
}
.prioritatenCardSingle .prioritatenCardMain h1{
    @apply text-primary-dark text-left;
}

.prioritatenCardSingle .prioritatenCardMain p{
    @apply text-primary-dark mx-0 text-lg w-full;
}

