.universityCardWrapper{
    @apply transition mb-5 ease-in-out duration-200 w-72 h-40 bg-white  rounded-2xl pt-6 pl-4 bg-opacity-40 text-white hover:bg-opacity-100 hover:text-black;
    @apply xl:w-[340px];
}

.universityCardWrapperIndexed {
    @apply mb-5 w-72 xl:w-[340px] h-40 bg-white  rounded-2xl pt-6 pl-4 text-black
}

.universityButton{

    @apply font-semibold px-3 py-1 rounded-3xl bg-sky-900 text-white;
}

.universityName {
    @apply text-2xl font-semibold mb-7;
}

/* Wuyika your Tailwind here */


.advantagesWrapper{
    @apply w-full rounded-3xl px-6 py-3 text-white font-medium text-[14px];
    @apply  md:px-10 md:py-12;
}

.advantagesTitle{
    @apply text-4xl font-semibold leading-none;
    @apply md:text-5xl;
    @apply lg:text-6xl; 
}

.advantagesCheck{
    @apply text-primary-teal mt-2 mr-1;
    @apply md:mr-3
}

.advantagesButton{
    @apply transition ease-out font-normal duration-300 border-[1.5px] border-white mb-6 py-1 px-3 text-white rounded-3xl mt-12 bg-transparent hover:text-black hover:bg-white hover:font-semibold;
    @apply mt-12 md:font-medium md:border-2;
}


.guarantyWrapper{
    @apply pt-20 pl-7 w-full rounded-xl shadow-xl ;
    @apply md:p-8 md:min-h-[400px] md:h-full md:pb-9;
}

.guarantyTitle{
    @apply mt-3  font-bold text-3xl break-words;
    @apply md:text-4xl;
}

.guarantyText{
    @apply mt-3 pr-10 pb-5 font-semibold text-base text-gray-900;
    @apply md:text-lg ;
}

.functionWrapper{
    @apply mx-2 px-10 pb-10 mb-10 shadow-xl;
    @apply md:shadow-none;
}

.functionIcon{
    @apply w-full flex justify-center;
}

.functionIconBadge{
    @apply px-3 py-0 text-sm text-white bg-[#59c2abfb] rounded-xl -ml-2 -mt-2;
    @apply md:px-4 md:text-base md:py-1 md:rounded-2xl
}

.functionTitle{
    @apply mt-16 font-bold text-xl text-center group-hover:text-[#59c2abfb];
}

.functionText{
    @apply mt-3 text-center text-gray-500;
}

.carousel{
    @apply  w-full
}


.gap {
    @apply w-2
}

.customerWrapper{
    @apply bg-white mb-5 rounded-2xl p-9 m-4;
    @apply md:w-[590px] md:h-96;
}

.customerHeader{
    @apply w-full flex flex-nowrap justify-center mb-4;
    @apply md:justify-start;
}

.customerImage{
    @apply h-20 w-20 rounded-full mr-5;
}

.customerName{
    @apply flex items-center text-3xl font-bold text-slate-800;
}

.customerText{
    @apply text-base text-justify text-slate-600 font-bold mt-4
}

/*Styling the blogCard*/
.blogCard{
    @apply h-fit w-96 shadow-xl rounded-t-3xl pb-5 lg:mr-5 mt-5;
}
.blogCardImage{
    @apply h-52 pt-5 rounded-t-3xl w-full bg-primary-dark/40 hover:bg-primary-dark/60 bg-blend-color shadow-lg  transition hover:ease-out hover:bg-blend-darken;
    @apply hover:cursor-pointer hover:bg-cover  bg-center  ;
}
.blogCardDate{
    @apply ml-6  w-fit px-4  rounded-3xl bg-primary-white/80 hover:cursor-text;
}
.blogCardDate p{
    @apply text-lg;
}
.blogCardBody{
    @apply p-5;
}
.blogTitle{
    @apply mb-2 text-center text-primary-dark  font-bold;
    @apply hover:cursor-pointer;
}

.blogText{
    @apply text-center ;
}

.blogbottom{
    @apply flex flex-row hover:cursor-pointer ml-3 justify-center items-center w-fit;
}

.blogBookmark{
    @apply text-xl text-primary-white font-bold rounded-full bg-primary-teal w-fit h-fit p-2 mr-2;

}
