.Die{
    @apply flex justify-center items-center bg-gradient-to-b from-primary-start to-primary-end;
}

.HeaderWrapper{
    @apply block p-6 w-[100%] mb-20;
    @apply md:max-w-6xl;
    
    
}

.MainTitle{
    @apply text-4xl font-bold mt-0 mb-6 text-center 
    text-primary-white py-20 px-6;
    
    @apply md:text-7xl;
}
.Card2{
    @apply block p-8 rounded-3xl shadow-lg bg-white  mb-10 self-center;
    @apply md:max-w-6xl md:p-16;
}

.Card2Header{
    @apply text-primary-dark text-5xl leading-tight font-bold mb-2 overflow-hidden;
    @apply md:text-7xl;
}

.Card2Text{
    @apply text-primary-dark text-base mb-4 font-medium;
}
.Card2Text p {
    @apply mb-3 text-lg font-[500];
}
.hiperlink1{
    @apply underline decoration-sky-500;
    hover:text-sky-400;
}




