.heading {
    @apply text-center text-6xl font-black;
}
.subHeading {
    @apply text-primary-grey font-bold text-center py-4 pb-8;
}
.wrapper {
    @apply py-4;
}
.nav {
    @apply w-fit m-0 md:m-auto;
}

/* Styles for incoporated nav bar */
.mainWrapper {
    @apply w-1/2 md:h-16 m-4;
}

.container {
    @apply flex-col md:flex-row w-fit bg-primary-white p-4;
    @apply h-full md:p-0 md:m-auto;
    @apply flex justify-around md:items-center;
    @apply text-primary-grey text-3xl md:text-xl lg:text-lg;
}

.NavLink::after {
    @apply bg-primary-teal block h-1 rounded-full;
    @apply origin-center w-0 mt-2;
    content: "";
    transition: width 0.3s;
}

.NavLink:focus::after {
    width: 100%;
}

.NavLink {
    @apply font-medium md:text-center px-4 py-2 hover:cursor-pointer;
    @apply text-gray-400 hover:text-gray-600 h-full;
}
.toggler {
    @apply h-8 w-8 block md:hidden;
}
.icon {
    @apply h-12 w-12 bg-primary-white;
}
.overall {
    @apply w-screen h-screen z-50 absolute md:relative;
}
