/*styling effiziente section*/
.effiziente{
    @apply flex flex-col lg:flex-row p-10 lg:p-16 justify-center w-11/12 lg:w-10/12 h-fit rounded-t-3xl rounded-3xl shadow-lg m-auto;
}
.effizienteLeft{
    @apply w-full lg:w-2/5 lg:mr-32;
}
.effizienteLeft h1{
    @apply text-4xl lg:text-7xl text-primary-dark font-bold mb-5;
}
.effizienteLeft p{
    @apply text-lg text-primary-dark font-semibold;
}

.effizienteChecklist{
    @apply mt-10 mb-10;
}

.effizienteChecklist h3{
    @apply text-3xl mb-5 text-primary-dark font-bold;
}
.effizienteChecklist div{
     @apply flex flex-row items-center mb-1 ;
}
.effizienteChecklist div p{
    @apply font-normal self-center;
}
.effizienteIcon{
    @apply text-3xl text-primary-green font-bold mr-2 self-center;
}
.effizienteRight{
    @apply w-full lg:w-3/5 ;
}

.seffizienteVideo{
    @apply w-full h-[60vh] rounded-3xl mb-2;
}

.effizienteRight h6{
    @apply text-sm;
}
/*styling studienordnung */
.studienordnung{
    @apply w-full mb-20;
}
.studienordnung h1{
    @apply text-4xl lg:text-7xl text-primary-dark text-center font-bold mb-5 mt-32  ;
}

.studienordnung h6{
    @apply text-xl font-normal mb-10 text-center;
}

.studienordnung p{
    @apply text-xl font-[400] lg:w-10/12 w-11/12 text-primary-dark mb-5 m-auto;
}

.studienordnung p span{
    @apply text-primary-green;
}

/* styling .professorImage section */

.professorImage{
    @apply w-full mb-20;
}

.professorImage img{
    @apply m-auto rounded-3xl hover:cursor-pointer w-11/12 lg:w-auto;
}

/* styling the Vorlesung section */

.vorlesung{
    @apply w-full my-20;
}

.vorlesung h1{
    @apply text-4xl lg:text-7xl font-bold text-center mb-5 text-primary-dark;
}
.vorlesung p{
    @apply text-primary-dark text-xl mb-5 text-center;
}

.vorlesungMain{
    @apply w-full flex flex-col lg:flex-row flex-wrap justify-center items-center;
}

.vorlesungMain div{
    @apply h-fit lg:h-[120vh] w-11/12 lg:w-[40vw]  p-10 lg:p-16 flex flex-col justify-start lg:mr-10 rounded-3xl mb-20;
}

.vorlesungMain div h1{
    @apply text-left text-primary-white;
}

.vorlesungMain div p{
    @apply text-left text-xl text-primary-white leading-8 mb-5;
}

.vorlesungRed{
    @apply bg-[#A7335A];
}
.vorlesungGreen{
    @apply bg-[#678A80];
}

.vorlesungTeal{
    @apply bg-[#1B374A];
}

.vorlesungYellow{
    @apply bg-[#C68731];
}

/* styling the Unietikette section */

.unietikette{
    @apply w-full my-20 py-20 h-fit bg-gradient-to-b from-[#6FA8B3] to-[#0A385A];
}

.unietikette h1{
    @apply text-center text-4xl lg:text-7xl text-primary-white font-bold mb-5;
}

.unietikette h5{
    @apply text-xl text-center mb-5 text-primary-white;
}

.unietiketteVideo{
    @apply w-11/12 lg:w-10/12 rounded-3xl h-[80vh] m-auto;
}
.unietikette h6{
    @apply text-sm mt-2 text-primary-white w-11/12 lg:w-10/12 m-auto;
}

.unietikette p{
    @apply text-primary-white my-5 m-auto w-11/12 lg:w-10/12 text-lg font-semibold;
}

.unietiketteMain{
    @apply flex flex-col lg:flex-row flex-wrap justify-center mt-20;
}

.unietiketteCards{
    @apply w-11/12 lg:w-[40vw]  lg:h-[105vh] h-fit lg:mr-10 lg:ml-0 m-auto bg-primary-white p-10 lg:p-16 rounded-3xl mb-10;
}

.unietiketteCards h1{
    @apply text-primary-dark text-left text-5xl lg:text-7xl ;
}

.unietiketteCards p{
    @apply text-primary-dark my-0 font-normal text-xl;
}