/*Styling the Arbeiten section*/

.arbeiten{
    @apply w-full lg:w-3/4 m-auto mb-20 px-6;
}

.arbeiten h1{
    @apply w-full text-5xl lg:text-7xl mb-10 text-primary-dark font-bold;
}

.arbeiten p{
    @apply text-xl text-primary-dark mb-5;
}



/*Bafog section styling*/
.bafog{
    @apply w-full h-fit py-10 pt-16 bg-primary-teal/70 my-20 ;
}

.bafogWrapper{
    @apply lg:w-3/4 w-full px-5 lg:px-0 m-auto;
}

.bafogWrapper h1{
    @apply text-7xl text-primary-white font-bold mb-10;
}

.bafogWrapper p{
    @apply text-lg text-primary-white font-semibold leading-6 mb-5;
}

.bafogWrapper p a{
    @apply text-primary-dark underline;
}

.BafogSmallWhiteWrapper{
    @apply flex lg:flex-row flex-col items-center justify-center mt-20;
}

/*styling the large white cards*/

.bafogLargeWhite{
    @apply w-full h-fit bg-primary-white rounded-3xl lg:p-20 p-10 mt-20;
}

.bafogLargeWhite h1{
    @apply text-5xl lg:text-6xl font-bold text-primary-dark ;
}

.bafogLargeWhite p{
    @apply text-primary-dark font-semibold leading-8;
}

/* styling the bafog small white card*/

.bafogSmallWhite{
    @apply lg:w-2/5 w-11/12  h-fit py-20 bg-primary-white mb-10 px-12 rounded-3xl  lg:mr-10;
}

.bafogSmallWhite h1{
    @apply text-5xl lg:text-7xl text-primary-dark font-bold mb-10;
}

.bafogSmallWhite p{
    @apply text-xl text-primary-dark/70 font-semibold leading-8 ;
}

.secondParagraph{
    @apply mt-5;
}


/*studienkredite*/
.studienkredite{
    @apply my-20;
}

.studienkrediteHeader{
    @apply m-auto lg:text-7xl text-6xl text-primary-dark font-bold text-center mb-10;
}

.studienkrediteBody{
    @apply w-11/12 lg:w-3/4 text-primary-dark text-lg font-semibold m-auto;
}

.ColoredCards{
    @apply w-full flex flex-col lg:flex-row justify-center  flex-wrap my-20;
}

.ColoredCardRed{
    @apply h-fit w-11/12 lg:pb-32 lg:mb-20 mb-10 lg:w-5/12 bg-[#A7345C] p-16 lg:mr-10 lg:m-0  m-auto rounded-3xl;
}

.ColoredCardRed h1{
    @apply lg:text-7xl text-6xl font-bold text-primary-white  mb-10;
}

.ColoredCardRed p{
    @apply text-primary-white text-lg font-bold;
}

.ColoredCardRed a{
    @apply text-primary-dark underline;
}

.ColoredCardTeal{
    @apply h-fit w-11/12 lg:w-5/12 bg-[#244052] p-16 mb-10 lg:m-0 m-auto  rounded-3xl;
}

.ColoredCardTeal h1{
    @apply lg:text-7xl text-6xl font-bold text-primary-white mb-10;
}

.ColoredCardTeal p{
    @apply text-primary-white text-lg font-bold;
}

.ColoredCardTeal a{
    @apply text-primary-dark underline;
}

.ColoredCardGreen{
    @apply h-fit mb-10 lg:w-5/12 w-11/12 bg-[#60857B] pb-28 p-16 lg:mr-10 lg:m-0 m-auto rounded-3xl;
}

.ColoredCardGreen h1{
    @apply text-6xl lg:text-7xl font-bold text-primary-white mb-10;
}

.ColoredCardGreen p{
    @apply text-primary-white text-lg font-bold;
}

.ColoredCardGreen a{
    @apply text-primary-dark underline;
}

.ColoredCardYellow{
    @apply h-fit lg:w-5/12 w-11/12 bg-[#C68333] pb-40 p-16 lg:m-0 m-auto rounded-3xl;
}

.ColoredCardYellow h1{
    @apply text-6xl lg:text-7xl font-bold text-primary-white mb-10;
}

.ColoredCardYellow p{
    @apply text-primary-white text-lg font-bold;
}

.ColoredCardYellow a{
    @apply text-primary-dark underline;
}

