.Die{
    @apply flex justify-center bg-gradient-to-b from-primary-start to-primary-end;
}

.HeaderWrapper{
    @apply block p-6 max-w-6xl mb-20;
}

.MainTitle{
    @apply text-5xl font-bold mt-0 mb-6 text-center 
    text-primary-white py-20 px-6;
    @apply md:text-7xl;
}

.Header1{
    @apply flex flex-col md:flex-row  mb-10 justify-center;
}

.image{
    background-image: url(../../../../resources/images/desk_mod_6.webp);
    @apply w-full h-full bg-cover bg-no-repeat bg-center rounded-3xl mt-4
    md:h-44 object-cover 
    md:w-48 rounded-3xl 
    md:rounded-none 
    md:rounded-3xl; 
}

.Card1{
    @apply p-6 flex flex-col justify-start;
}

.Card1Text{
    @apply text-primary-white text-base mb-4 font-medium;
}

.Card2{
    @apply block p-16 rounded-3xl shadow-lg bg-white max-w-6xl mb-10;
}

.Card2Header{
    @apply text-primary-dark text-7xl leading-tight font-bold mb-2;
}

.Card2Text{
    @apply text-primary-dark text-base mb-4 font-medium;
}
.Card2Text p {
    @apply mb-3 text-lg font-[500];
}

/***********  Needed *************/

.CardPosition{
    @apply block bg-white p-10 rounded-3xl overflow-hidden 
    m-auto relative px-10 mb-8 mt-8 w-full ;
    @apply md:w-1/2;
    @apply lg:w-1/2;
}
.CardPosition1{
    @apply block space-x-10 p-10  m-auto ;
    @apply lg:flex lg:w-1/2 ;
}


.CardWrapper{
    @apply bg-gradient-to-b from-cyan-300 to-sky-900 p-8;
}

.ImageStyle{
    background-image: url(../../../../resources/images/desk_mod_6.webp);
    @apply bg-cover bg-no-repeat bg-center rounded-3xl space-x-4;
    @apply sm:w-full sm:h-96 m-auto mb-8 sm:block ;
    @apply w-64 h-3/4 rounded-3xl m-auto my-10 mb-5;
}

.CardHeader1{
    @apply text-4xl lg:text-7xl ml-8 text-primary-white font-bold mb-10 text-center;
    @apply sm:text-5xl;
}

.CardHeader2{
    @apply text-4xl lg:text-7xl lg:w-full text-primary-dark font-bold mb-10;
    @apply sm:text-5xl;
}

.CardText{
    @apply  text-2xl font-medium justify-center text-inherit;
}