.aboutBody {
    @apply  rounded-3xl block py-5 px-2 max-w-7xl my-5 grid gap-0;
    @apply md:p-10 md:flex md:gap-8 md:grid-cols-2;
    @apply lg:p-20 lg:justify-around lg:gap-16 lg:my-10 lg:mx-auto;
}

.aboutMain {
}

.aboutText {
    @apply font-bold text-base;
}

.aboutImage {
    @apply mt-12 w-[78rem];
    @apply md:my-auto md:w-[84rem];
}

.aboutHeading {
    @apply text-7xl font-bold;
}


.figWrapper {
    @apply mb-2;
    @apply md:my-auto;
}

.figImage {
    @apply lg:h-full  my-auto mx-auto w-64;
    @apply lg:w-[25.5rem];
}

.figCaption {
    @apply font-semibold text-xs break-words text-center pt-5 px-3;
}

.founderFigure {
    @apply w-96 my-0 mx-auto;
    @apply lg:h-full;
}

.founderImage{
    @apply rounded-3xl h-full;
}