.navBody {
    @apply w-screen h-12 bg-transparent;
}

.container {
    @apply w-fit gap-3 flex m-auto text-primary-white p-2 font-semibold;
}

.navElement {
    @apply hover:text-primary-teal;
}
.subMenuWrapper {
    @apply invisible flex flex-col;
}
.menu:hover .subMenuWrapper {
    @apply visible h-auto  opacity-100 shadow-md bg-transparent p-4 gap-3 rounded-2xl;
}

.dropDown{
    @apply w-32 flex items-center justify-between;
}
