.buttonDiv {
    @apply hidden;
}

/* Body */
.body {
    @apply shadow-xl bg-transparent backdrop-grayscale bg-gradient-to-r from-gray-200 to-green-100;
    @apply flex-col flex;
    @apply p-5;
    @apply xl:w-11/12 lg:min-w-[800px] lg:h-5/6 lg:gap-20;
    @apply lg:flex lg:flex-row mx-auto md:px-12 xl:px-32 xl:py-32 rounded-3xl;
}

.wrapper {
    @apply px-12 py-12 pb-24;
    @apply lg:mt-[-10rem] lg:w-screen xl:px-48 overflow-hidden lg:py-28;
    /* min-width: 400px; */
}
.image {
    @apply w-48 lg:w-48 h-96 pb-8 mx-auto my-0 px-4  xl:py-4 xl:h-full lg:px-3 lg:pb-6 rounded-xl;
}

.cardHeading {
    @apply font-black text-4xl lg:text-6xl;
}

.text1 {
    @apply text-[#005c81];
}

.text3 {
    @apply mt-2 md:w-11/12 lg:w-10/12;
}
.text1,
.text2,
.text3 {
    @apply text-sm;
    @apply md:text-xl md:font-normal md:leading-relaxed;
    @apply lg:text-base font-bold;
}
.imageDiv{
    @apply py-5 lg:py-3 h-96 mx-auto my-0 w-72 md:w-72 lg:w-80 bg-primary-white;
    background: url(../../resources/images/iphonex.svg) no-repeat center;
    min-width: 30%;
}
.leftSide {
    @apply order-1;
    @apply lg:order-last;
}
