.StellenanzeigenCard {
  @apply h-[110vh] lg:w-[26%] md:w-[33%]  
    pb-0 w-11/12 rounded-2xl bg-primary-grey-lighter 
    lg:mr-3 flex flex-col items-center mb-20 relative 
    shadow-primary-grey shadow-md;
}

.backgroundTeal {
  @apply w-full h-28 bg-primary-teal flex flex-col rounded-t-xl justify-center text-primary-white items-center;
}

.backgroundTeal h3 {
  @apply text-2xl font-bold;
}
.backgroundTeal p {
  @apply text-sm;
}
.stellenanzeigenPrice {
  @apply text-center mt-0;
}
.stellenanzeigenPrice .price {
  @apply text-7xl font-bold text-primary-green mb-0 flex mt-3;
}

.stellenanzeigenPrice .price span{
    @apply text-2xl text-start mt-2;
}
.stellenanzeigenPrice .priceWord{
    @apply text-4xl font-bold text-primary-green mb-0 flex mt-3;
}
.StellenanzeigenList {
  @apply mt-10 mb-20 w-full px-3;
}

.StellenanzeigenList ul li {
  @apply flex flex-row justify-start mb-3 text-sm font-semibold text-primary-dark items-center;
}

.StellenanzeigenListIcon {
  @apply  text-xl self-center w-full;
}

.greenBadge {
  @apply w-fit h-fit px-3 py-2 left-0 bg-primary-green rounded-r-2xl flex justify-center items-center absolute top-5;
}
.greenBadge p {
  @apply text-primary-white font-bold text-sm;
}

.actionButton{
  @apply absolute bottom-0 mb-10;
}