.span1 {
    @apply absolute inset-0;
    @apply border-0 group-hover:border-[25px] border-white rounded-full;
    @apply ease-linear duration-100 transition-all;
    @apply hover:text-black;
}
.span2 {
    @apply relative w-full;
    @apply text-center text-white;
    @apply transition-colors duration-200 ease-in-out;
}
.link {
    @apply w-28 md:w-72 md:h-12 lg:w-44 mt-[-1rem] mb-5;
    @apply relative inline-flex items-center justify-start inline-block overflow-hidden;
    @apply px-1 lg:px-3 py-3  font-medium  lg:opacity-75;
    @apply transition-all bg-[#63ae9c] rounded-full hover:opacity-100;
}

.link2 {
    @apply bg-[#005c81];
}

.buttons {
    @apply flex  space-x-4 lg:space-x-8 mt-16;
}

/* Body */
.body {
    @apply rounded-3xl shadow-xl bg-transparent backdrop-grayscale bg-gradient-to-r from-gray-200 to-green-100;
    @apply block p-5 gap-1;
    @apply md:block md:px-12 md:py-24;
    @apply lg:flex lg:mx-auto xl:px-32 xl:py-32 lg:gap-8 xl:w-11/12 lg:min-w-[800px] lg:h-5/6;
}

.wrapper {
    @apply px-12 pt-10;
    @apply lg:w-screen xl:px-48 lg:overflow-hidden lg:py-28;
}

.image {
    @apply scale-100;
    @apply md:scale-75 md:m-0;
    @apply lg:scale-100 lg:mt-[-1rem];
}

.cardHeading {
    @apply font-black text-4xl lg:text-6xl;
}

.text1 {
    @apply text-[#005c81];
}

.text3 {
    @apply w-11/12;
    @apply mt-2 lg:w-10/12;
}
.text1,
.text2,
.text3 {
    @apply text-sm;
    @apply md:text-xl md:font-normal md:leading-relaxed;
    @apply lg:text-base  font-bold;
}

