.Lernen{
    @apply flex justify-center mb-20;
}

.HeaderWrapper{
    @apply block p-6  max-w-6xl;
}

.Header{
    @apply text-primary-dark text-6xl  leading-tight
     font-bold font-medium mb-10;
    @apply lg:text-8xl;
}

.HeaderText{
    @apply text-gray-700 text-base mb-16 font-[500] text-xl;
}



.HeaderButton{
        @apply outline outline-black px-6 py-3 
        font-medium text-lg leading-tight
        hover:bg-black hover:text-white 
        rounded-full ease-in-out;
}
