.lifeEasy {
    @apply h-fit  bg-primary-teal bg-cover;
    @apply mt-0 pt-5 lg:mt-10 lg:py-32 bg-fixed;
    background-image: url(../../../resources/images/blue-bg1.jpg);
}

.learningShockWrapper {
    @apply w-full h-full xl:w-10/12 mx-auto;
}

.learningshockHeader {
    @apply text-center text-3xl md:text-5xl font-bold text-primary-white;
    @apply pt-12 lg:text-4xl lg:p-10;
}
.cardSection {
    @apply grid md:grid-cols-2 lg:grid-cols-3;
    @apply gap-12 mt-5 p-10 lg:py-8 lg:px-20;
}
/* Buuton styles */
.span1 {
    @apply absolute inset-0;
    @apply border-0 group-hover:border-[25px] border-white rounded-full;
    @apply ease-linear duration-100 transition-all;
    @apply hover:text-black;
}
.span2 {
    @apply relative w-full;
    @apply text-center text-white;
    @apply transition-colors duration-200 ease-in-out;
}
.link {
    @apply w-32 h-12 md:w-36 lg:w-44;
    @apply relative inline-flex items-center justify-start inline-block overflow-hidden;
    @apply px-1 lg:px-3 py-3  font-medium  opacity-75;
    @apply transition-all bg-[#63ae9c] rounded-full hover:opacity-100;
}

.link2 {
    @apply bg-[#005c81];
}

.buttons {
    @apply flex justify-center items-center space-x-8 mt-10;
}

.card2 {
    @apply mx-auto p-12 overflow-visible w-full gap-16 h-3/5 py-32;
}

