/*styling uberlernen section */

.uberLernen{
    @apply w-full mb-20;
}

.uberLernen h1{
    @apply text-center text-4xl lg:text-7xl font-bold mb-10 text-primary-dark;
}

.uberLernen h6{
    @apply text-xl text-center text-primary-dark mb-10;
}

.uberLernen p{
    @apply w-11/12 lg:w-3/4 m-auto text-xl text-primary-dark mb-3 font-[400];
}

/*styling lernverhalten section */

.lernverhalten{
    @apply w-full my-20;
}

.lernverhalten h1{
    @apply text-4xl lg:text-7xl font-bold text-center text-primary-dark mb-5;
}

.lernverhalten h6{
    @apply text-2xl text-primary-dark text-center mb-10;
}

.lernverhaltenBulb{
    @apply w-11/12 lg:w-3/4 m-auto items-center my-10 flex flex-col lg:flex-row justify-between ;
}
.lernverhaltenMain{
    @apply mt-10 lg:mt-0 lg:ml-20;
}
.lernverhaltenMain div{
    @apply flex flex-row items-center mb-1;
}

.lernverhaltenIcon{
    @apply text-3xl text-primary-green font-bold mr-5;
}

.lernverhaltenMain p{
    @apply mt-0 w-full text-xl;
}

.lernverhaltenparagraph{
    @apply text-lg w-11/12 lg:w-3/4 text-primary-dark font-semibold mb-5 m-auto;
}

/*styling lernstrategienFive section */

.lernstrategienFive{
    @apply w-full my-20 py-10 bg-[#FFFBF8] h-fit ;
}

.lernstrategienFive h1{
    @apply text-4xl lg:text-7xl text-center font-bold text-primary-dark my-20;
}

.lernstrategienFive p{
    @apply w-11/12 lg:w-3/4 mb-5 text-xl font-[400] m-auto;
}

.lernstrategienFive a{
    @apply underline hover:text-primary-teal;
}

.weiters{
    @apply w-11/12 w-3/4 m-auto my-10;
}
.weiters div{
    @apply flex flex-row items-center;
}
.weitersIcon{
    @apply text-3xl text-primary-green font-bold self-center;
}

.weiters  p{
    @apply mt-0 w-full ml-5 self-center mt-3;
}

.weiters h3{
    @apply text-3xl lg:text-4xl text-primary-dark font-bold mb-5;
}

.lernstrategienVideo{
    @apply flex justify-between m-auto flex-col lg:flex-row w-full w-3/4 ;
}

.videoFrame{
    @apply w-[80vw] lg:w-[36vw] h-72 rounded-3xl mb-5 ml-0;
}

.lernstrategienFive h5{
    @apply w-11/12 lg:w-3/4 text-primary-dark text-sm m-auto;
}

/*styling umsetzung section*/

.umsetzung h1{
    @apply text-4xl lg:text-7xl font-bold text-center text-primary-dark mb-5;
}

.umsetzung p{
    @apply w-11/12 lg:w-3/4 mb-5 text-xl font-[400] m-auto;
}

.umsetzung h6{
    @apply text-2xl text-primary-dark text-center mb-10;
}

.umsetzung h5{
    @apply w-11/12 lg:w-3/4 text-primary-dark text-sm m-auto;
}

/* styling volleyBalImage section*/

.volleyBalImage{
    @apply w-full my-20;
}

.volleyBalImage img{
    @apply m-auto rounded-3xl w-11/12 lg:w-auto;
}