.wrapper{
    @apply flex justify-center ;
}

.spacer{
    @apply mb-10;
}

.MainTitle{
    @apply text-6xl font-bold text-center 
    text-primary-dark py-4 px-6;
    @apply md:text-7xl;
}
.SecondTitle{
    @apply text-xl  mt-0 mb-6 text-center 
    text-primary-dark px-6;
    @apply md:text-xl;
}

iframe{ 
    @apply block justify-center; 
    @apply bg-cover bg-no-repeat bg-center rounded-3xl;
    
}

.Header1{
    @apply flex flex-col md:flex-row  mb-10 justify-center;
}

.image{
    background-image: url(../../../../../resources/images/Productivity.webp);
    @apply bg-cover bg-no-repeat bg-center rounded-3xl space-x-4;
    @apply w-full h-auto;
}


.Card2{
    @apply block p-10 bg-white max-w-5xl mb-4;
}

.Card2Header{
    @apply text-primary-dark text-7xl leading-tight font-bold mb-2;
}

.Card2Text{
    @apply text-primary-dark text-base mb-4 font-normal text-xl leading-normal ;
}

.Card3Text{
    @apply text-2xl text-primary-dark text-base mb-4 font-medium leading-normal ;
}

.Card3Text p {
    @apply mb-3 text-lg font-[500];
}

.Card4Text{
    @apply text-primary-dark text-lg mb-4 font-medium;
}
/***********  Needed *************/

.CardPosition{
    @apply block bg-white p-10 rounded-3xl overflow-hidden 
    m-auto relative px-10 mb-8 mt-8 w-full ;
    @apply md:w-1/2;
    @apply lg:w-1/2;
}
.CardPosition1{
    @apply block space-x-10 p-10  m-auto ;
    @apply lg:flex lg:w-1/2 ;
}


.CardWrapper{
    @apply bg-gradient-to-b from-cyan-300 to-sky-900 p-8;
}

.CardHeader2{
    @apply text-4xl lg:text-7xl lg:w-full text-primary-dark font-bold mb-10;
    @apply sm:text-5xl;
}

.CardText{
    @apply  text-2xl font-medium justify-center text-inherit;
}