.wrapper {
    @apply w-full xl:w-1/2 h-3/4 flex flex-col;
}

.container {
    @apply flex flex-col gap-1 m-auto ;
}

.modulsubNav {
    @apply ml-8 flex flex-col gap-2;
}

.subNavWrapper {
    @apply flex items-center h-8;
}

.link {
    @apply hover:text-teal-200 text-gray-800 font-medium;
}

.link:active{
    @apply text-teal-200;
}

.iconDiv {
    @apply h-fit text-3xl;
    @apply hover:border hover:border-teal-200 border-dotted;
}

.heading{
    @apply text-3xl py-4 font-bold text-gray-800;
}

