.Wrapper {
  @apply justify-center;
  background-color: #F0F2F5;
}

.hero {
  background-image: url(../../../resources/images/Pitch.webp);
  @apply w-full h-full bg-cover bg-no-repeat bg-center p-40;
}

.hero h2 {
  @apply text-7xl font-bold text-primary-white mx-auto text-center;
}

.download {
  width: 100%;
  display: flex;
  padding: 20px 60px;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  height: 90vh;
}

.download .left {
  width: 50%;
}

.left h2 {
  font-size: 2rem;
  font-weight: 700;
}

.left p {
  font-size: 17px;
  color: #7c7d7e;
}


.left .btns {
  /* margin: 20px; */
}

.left button {
  padding: 10px 20px;
  box-sizing: border-box;
  border: 2px solid #005c81;
  border-radius: 50px;
  color: #005c81;
  transition: all .5s ease;
  margin: 20px;
}

.left button h4, .left button p {
  padding: 0;
  margin: 0;
  
}

.left button p {
  font-size: 14px;
  color: #005c81;
}

.left button:hover {
  background-color: #005c81;
  color: #fff;
  transition: all .5s ease;
}

.left button:hover p {
  color: #fff;
  transition: all .5s ease;
}

.download .right {
  width: 50%;
}

.right img {
  width: 80%;
}

.template {
  width: 100%;
  height: 100vh;
  padding: 20px 60px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.template .txt, .template .carousel {
  width: 50%;
}

.txt h2 {
  font-size: 2rem;
  font-weight: 700;
}

.txt p {
  color: #7c7d7e;
  margin: 15px;
  flex: 15px;
}

.txt button {
  padding: 8px 16px;
  box-sizing: border-box;
  border: 2px solid #005c81;
  border-radius: 50px;
  color: #005c81;
  margin: 15px;
}

.txt button:hover {
  background-color: #005c81;
  color: #fff;
}

.swiper {
  width: 90%;
}

.text {
  width: 100%;
  height: 100vh;
  padding: 20px 60px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}

.text .wrapper {
  width: 60%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.text .wrapper h2 {
 font-size: 2rem;
 font-weight: 700;
}

.text .wrapper p {
  flex: 15px;
  color: #7c7d7e;
  margin: 15px;
  margin-left: 0;
}

.text .wrapper .down {
  margin-top: 15px;
}

.text .wrapper button {
  padding: 8px 16px;
  box-sizing: border-box;
  border: 2px solid #005c81;
  border-radius: 50px;
  color: #005c81;
  transition: all .7s ease-in-out;
  font-weight: 600;
  margin-right: 20px;
}

.text .wrapper button:hover {
  background-color: #005c81;
  color: #fff;
  transition: all .7s ease-in-out;
}








































.cardText1 {
  @apply text-primary-dark mt-8 text-xl font-medium;
}

.cardHead {
  font-size: 2rem !important;
  font-weight: 700 !important;
  margin-bottom: 1rem !important;
}

.middleFlex {
  @apply grid lg:grid-cols-2 justify-between p-6 space-y-8 
     mb-10;
  width: 100% !important;
  height: 80vh !important;
}

.cardWrapper {
  width: 100% !important;
  padding: 0 30px !important;
  box-sizing: border-box !important;
  height: 100% !important;
  display: flex !important;
  flex-direction: column;
  justify-content: center !important;
}

.rightFlex {
  @apply grid lg:grid-cols-1 p-8  rounded-3xl 
    shadow-lg mb-10;
}
.leftFlex {
  @apply grid lg:grid-cols-2 justify-between p-6 space-y-8 max-w-6xl 
    mb-10;
}

.image {
  @apply  h-80 bg-cover bg-no-repeat bg-center mb-10 m-auto h-auto;
  width: 60% !important;
  display: flex;
align-items: center;
}

.buttonWrapper {
  @apply flex space-x-8;
}

.buttonWrapper button {
  padding: 8px 16px !important;
  border: 2px solid #005c81 !important;
  border-radius: 50px !important;
  color: #005c81 !important;
  font-weight: 600 !important;
  margin-top: 2rem !important;
}

.buttonWrapper button:hover {
  background-color: #005c81;
  color: #fff !important;
}

.buttonWrapper button .p {
    font-size: .7rem;
    margin: 0;
}

.TextTitle1 {
  @apply text-primary-dark text-4xl text-base mb-4 font-bold;
}

.TextTitle2 {
  @apply text-primary-dark text-base mb-4 font-normal;
}


.Card1 {
  @apply p-6 flex flex-col justify-start;
}

.Card1Text {
  @apply text-primary-dark text-base mb-4 font-medium;
}

.socialMedia {
  width: 100%;
  height: 100vh;
  padding: 10px 40px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
}

.socialMedia .leftSection {
  flex: 1;
  padding: 0 30px 0 0;
  box-sizing: border-box;
}

.socialMedia .leftSection h1 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.socialMedia .leftSection p {
  font-size: 1rem;
  width: 90%;
  margin-bottom: 1.5rem;
}

.socialMedia .leftSection button {
  padding: 8px 16px;
  border: 2px solid #005c81;
  border-radius: 50px;
  font-size: 1.5rem;
  color: #005c81;
}

.socialMedia .leftSection button:hover {
  background-color: #005c81;
  color: #fff;
}

.socialMedia .rightSection {
  flex: 1;
  width: 50%;
  height: 100%;
  background-color: #005c81;
}

.swiperWrapper {
  width: 100%;
  height: 100%;
}
.slider {
  width: 100%;
  height: 100%;
}

.lastSection {
  width: 100%;
  height: 80vh;
  padding: 0 40px;
  box-sizing: border-box;
}

.lastSection .sectionWrapper {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.lastSection .sectionWrapper h1 {
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 1.2rem;
  text-transform: capitalize;
}
.middleFlex{
    @apply grid lg:grid-cols-2 m-auto max-w-screen-2xl max-h-96 rounded-3xl  mt-28;
}

.firstWrap{
    @apply  m-auto;
}
.secondWrap{
    @apply  m-auto;
}

.cardTitle1{
    @apply text-primary-dark text-4xl font-bold mb-4 mx-auto;
}

.cardTitle2{
    @apply text-primary-dark text-[#616161] font-normal mb-4 mx-auto;
}

.buttonWrapper{
    @apply text-left ml-6;
}

.button1{
    @apply outline outline-1 px-6 py-3 
    font-normal text-lg text-[#005C81] leading-tight
    hover:bg-[#005C81] hover:text-white 
    rounded-full ease-in-out;
}

.button2{
    @apply outline outline-1 px-6 py-3 
    font-normal text-lg text-[#005C81] leading-tight
    hover:bg-[#005C81] hover:text-white 
    rounded-full ease-in-out;
}

.image{
    @apply w-10/12 h-5/6 bg-cover bg-no-repeat bg-center mb-10 m-auto h-auto mt-8;
}

