.sogelingtCard {
  @apply flex flex-col py-12 px-8 md:p-12 sm:p-8 h-fit w-full mx-auto my-8 bg-primary-white rounded-3xl mt-10;
}
.container {
  @apply w-full h-full bg-primary-white p-0 md:p-4;
}

.sogelingtHeader {
  @apply w-3/4 mb-4;
}
.sogelingtHeader h1 {
  @apply text-4xl font-bold text-primary-dark;
  @apply md:text-4xl;
  @apply lg:text-7xl;
}

.sogelingtBody {
  @apply w-full mb-3;
}

.sogelingtBody p {
  @apply w-full  text-xl text-left;
}
.sogelingtList {
  @apply text-xl text-left;
}
