/* styling studieren section */
.studieren {
  @apply w-full mb-20;
}

.studieren h1 {
  @apply mt-32 text-4xl lg:text-7xl font-bold text-center text-primary-dark mb-5;
}

.studieren p {
  @apply mb-3 text-lg w-11/12 lg:w-3/4 m-auto font-[500];
}

.studieren p a {
  @apply underline;
}

/*styling the module video section */

.moduleTenVideo {
  @apply w-full my-20;
}

.moduleTenVideo h1 {
  @apply text-4xl lg:text-7xl text-center text-primary-dark font-bold mb-5;
}

.moduleTenVideo h6 {
  @apply text-2xl text-primary-dark text-center mb-10;
}

.moduleVideo {
  @apply w-11/12 lg:w-3/4 h-[75vh] m-auto rounded-3xl mb-1;
}
.videoDescription {
  @apply text-sm mb-10 w-11/12 lg:w-3/4 m-auto;
}
.moduleTenVideo p {
  @apply w-11/12 lg:w-3/4 m-auto mb-3 text-lg text-primary-dark font-[500];
}

.videoDescription span {
  @apply font-bold;
}

/*styling institutionne section */

.institutionen {
  @apply w-full my-20;
}

.institutionen h1 {
  @apply text-4xl lg:text-7xl text-center text-primary-dark font-bold mb-5;
}

.institutionen h6 {
  @apply text-2xl text-primary-dark text-center mb-10;
}

.institutionenMain {
  @apply w-11/12 lg:w-3/4 m-auto my-10 h-fit p-10 shadow-xl;
}
.mainDiv p {
  @apply my-5 text-xl font-semibold hover:cursor-pointer;
}
.subDiv {
  @apply p-5 px-0 pt-2;
}
.subDiv p {
  @apply text-sm mb-3 font-normal;
}

.subDiv a {
  @apply underline;
}
.subDiv ul {
  @apply list-disc;
}

.subDiv ul li {
  @apply text-sm mb-3 font-normal;
}
.divunderline {
  @apply w-full h-0.5 bg-primary-grey/20;
}
