.fullyAutomated {
    @apply h-fit w-full md:w-10/12 lg:w-10/12 xl:w-8/12;
    @apply flex flex-col justify-center items-center;
    @apply my-24 md:my-0 mx-auto;
    @apply lg:gap-1 lg:mb-32 lg:mt-2;
}
.fullyautomatedText {
    @apply w-full mt-4  pt-24 mb-10  xl:mb-32 lg:pt-2 lg:mt-32 lg:pt-5 xl:pt-0 xl:mt-1 xl:mt-52;
}
.fullyautomatedText h1 {
    @apply text-3xl md:text-3xl;
    @apply lg:text-5xl xl:text-6xl text-primary-dark font-bold text-center;
}
.fullyautomatedText h3 {
    @apply text-2xl mt-5 md:text-2xl lg:text-4xl text-center font-bold;
}
.fullyAutomatedMain {
    @apply justify-center flex flex-col;
    @apply lg:flex-row;
    @apply gap-4 px-2 lg:gap-16;
}

.cardDiv {
    @apply mx-auto md:my-0 w-full;
}



