.cardWrapper{
    @apply w-full md:w-9/12 lg:w-7/12 m-auto bg-[#447064] h-fit xl:h-4/5 rounded-3xl p-10;
}
.card{
    @apply h-fit flex flex-col justify-around;
    @apply p-0 md:p-8 text-primary-white;
}
.cardHeading{
    @apply text-5xl md:text-6xl lg:text-7xl font-bold;
}
.text{
    @apply text-lg;
}