.masterSemester{
    @apply w-full flex flex-col justify-center items-center mt-20;
}

.masterSemesterHead h1{
    @apply text-center mb-20 text-3xl lg:text-7xl text-primary-dark font-bold;
}

.masterInform{
    @apply w-11/12 lg:w-3/4 p-16 flex flex-col bg-primary-green/100 rounded-3xl h-fit;
}

.MasterInformHead{
    @apply w-2/4 mb-5 ;
}

.MasterInformHead h1{
    @apply text-4xl lg:text-7xl text-primary-white font-bold;
}

.masterInformBody{
    @apply w-full mt-5 ;
}

.masterInformBody p{
    @apply w-full text-xl text-primary-white;
}


.masterSemesterDouble{
    @apply w-full flex flex-col lg:flex-row justify-center items-center mt-20;
}

.masterSemesterLeft{
    @apply lg:w-2/5 w-11/12 h-fit p-16 rounded-3xl bg-primary-red lg:mr-10 mb-10 lg:mb-0;
}
.masterSemesterLeft h1{
    @apply lg:text-7xl text-4xl font-bold text-primary-white mb-5;
}

.masterSemesterLeft h3{
    @apply lg:text-4xl text-2xl text-primary-white mb-5;
}

.masterSemesterLeft p{
    @apply text-xl text-primary-white;
}


.masterSemesterRight{
    @apply  lg:w-2/5 w-11/12 h-fit p-16 rounded-3xl bg-primary-teal/70 pb-40 mt-10 lg:mt-0;
}

.masterSemesterRight h1{
    @apply lg:text-7xl text-4xl font-bold text-primary-white mb-10;
}



.masterSemesterRight p{
    @apply text-xl text-primary-white;
}