.moduleTop{
    @apply w-full h-screen bg-primary-teal mb-20;
}

.moduleTopWrapper{
    @apply w-full flex flex-col h-full justify-center p-32;
}

.moduleTopHead{
    @apply w-1/2;
}

.moduleTopHead h1{
    @apply lg:text-7xl text-5xl text-primary-white font-bold;
}
.moduleTopbody{
    @apply w-full;
}
.moduleTopbody p{
    @apply text-2xl text-primary-white font-semibold mt-8;
}