.homeTop{
    @apply w-full h-fit bg-cover bg-no-repeat lg:p-20 md:p-16 sm:p-10;
}
.homeTopHead{
    @apply mb-10;
}
.homeTopHead h1{
    @apply text-4xl lg:text-7xl  text-primary-white font-bold leading-10;
}

.homeTop p{
    @apply text-xl  text-primary-white mb-5;
}

.homeTopMove{
    @apply flex flex-row w-32 justify-between capitalize text-primary-grey-light font-bold text-sm items-center border-b-[1px] border-primary-grey-light;
}

.homeTopMoveIcon{
    @apply text-xl font-bold;
}

.findWhat {
    width: 100%;
}

.findWhat .links button {
    color: rgb(151, 127, 127);
    font-size: 1.2rem;
    padding:  10px;
    font-weight: 500;
}

.findWhat .links button:hover , .findWhat .links button:active {
    color: #fff;
}

.findWhat .contents {
    width: 100%;
    display: flex;
    background-color: #fff;
    padding: 30px 10px;
    border-radius: 15px;
}

.findWhat .contents .content {
    display: none;
    color: black;
    flex: 2;
}

.findWhat .contents .find {
    font-weight: 600;
    flex: 1;
    color: #fff;
    width: 379px !important;
    height: fit-content !important;
    font-size: 1.2rem !important;
    padding: 8px 16px !important;
    border-radius: 25px !important;
    background-color: #005C81;
    transition: all .2s ease;
    display: flex;
    justify-content: center;
    align-items: center;
}

.activecontent {
    flex: 3;
    display: flex;
    justify-content: center;
    align-items: center;
}

.activecontent input, .activecontent select {
    border: none;
    border-bottom: 2px solid rgb(183, 183, 183);
    margin: 0px 10px;
    width: 30% !important;
}


.findWhat .contents .find .search {
    margin-right: 10px;
    font-size: 1.2rem;
}





