.aboutBody {
    @apply rounded-3xl block py-5 px-2 max-w-7xl my-5 grid gap-0;
    @apply md:p-10 md:flex md:gap-8 md:grid-cols-2;
    @apply lg:p-20 lg:justify-around lg:gap-16 lg:my-10 lg:mx-auto;
}

.bgaboutBody {
    @apply rounded-3xl bg-cover block py-5 px-2 max-w-5xl my-5 grid gap-0 bg-no-repeat bg-center shadow-2xl;
    @apply md:flex md:gap-8 md:grid-cols-2;
    @apply lg:p-52 lg:justify-around lg:gap-16 lg:my-10 lg:mx-auto;
    background-image: url(../../../resources/images/nachfolgezentrale-mv.jpg);
}

.aboutMain {
    @apply text-left text-white;
}

.aboutHeading {
    @apply text-7xl font-bold;
}

.firstText{
    @apply flex font-bold text-xl text-center justify-center;
    @apply lg:text-7xl lg:mt-10;
}

.cardstyle{
    @apply block py-10 p-2 rounded-3xl shadow-lg max-w-xl;
    @apply lg:p-20 
    
}

.firstsectionWrapper {
    @apply bg-fixed h-max bg-cover p-20;
    /* @apply md:h-full md:w-max md:bg-fixed; */
}

.aboutOverlay {
    @apply backdrop-blur-sm bg-opacity-80 h-full p-5;
    @apply md:py-6 md:px-14;
    @apply lg:py-8 lg:px-32; 
}

