.Lernen{
    @apply flex justify-center;
}

.HeaderWrapper{
    @apply block p-6  max-w-6xl;
}

.Header{
    @apply text-primary-dark text-6xl leading-tight font-bold font-bold mb-10;
}

.HeaderText{
    @apply text-gray-700 text-base mb-16;
}

/* .Stand{
    @apply lg:ml-96 m-auto ;
} */
.Stand{
    @apply grid w-auto h-auto justify-center  space-y-8 ;
}

.Fly{
    @apply ml-80;
}


.HeaderButton{
        @apply outline outline-black px-6 py-3 
        font-medium text-lg leading-tight
        hover:bg-black hover:text-white 
        rounded-full ease-in-out;
}

.secondsectionWrapper{
    @apply bg-white ;
    
}

.cardPosition{
    @apply p-10 rounded-3xl overflow-hidden relative px-8 py-20 mb-6;
}

.cardPositionMiddle{
    @apply bg-amber-500 p-10 rounded-3xl overflow-hidden relative px-8 py-8 mb-10 h-96;
}
.CardHeader1{
    @apply text-6xl font-bold text-center 
    text-primary-dark py-10 px-6;  
}

.cardTitle{
    @apply text-primary-white text-6xl leading-tight font-bold mb-24 ;
}
.cardTitle1{
    @apply text-primary-dark p-2 text-5xl leading-tight font-bold mb-6;
}

.cardText{
    @apply text-primary-white mt-8 text-xl font-medium;
}

.CardText1{
    @apply text-primary-dark mt-8 text-xl font-medium;
}
.middleFlex{
    @apply grid lg:grid-cols-2 justify-between p-6 space-y-8 max-w-6xl rounded-3xl 
    shadow-lg mb-10;
}
.rightFlex{
    @apply grid lg:grid-cols-1 p-8  rounded-3xl 
    shadow-lg mb-10;
}
.leftFlex{
    @apply grid lg:grid-cols-2 justify-between p-6 space-y-8 max-w-6xl 
    mb-10;
}

.image{
    @apply w-72 h-80 bg-cover bg-no-repeat bg-center mb-10 m-auto h-auto ;
}