.carouselWrapper {
    @apply w-screen lg:h-fit py-16 md:pt-32  mx-auto my-5 md:my-12 lg:my-32 lg:pt-5 lg:py-12;
}
.text {
    @apply hidden text-primary-white font-bold px-3;
}
.heading {
    @apply hidden text-2xl md:text-3xl font-bold text-primary-white p-3;
}
.headingShow {
    @apply text-2xl md:text-3xl font-bold text-primary-white p-3;
}
.textShow {
    @apply text-primary-white font-bold px-3;
}
.fullCard {
    @apply w-full h-[500px] opacity-100 hover:opacity-90 hover:cursor-grab;
    @apply transition-shadow transition ease-in-out delay-150 duration-300;
    @apply pt-96 pb-32 px-4 my-0 lg:my-16 xl:pt-32 xl:pb-0;
    @apply flex flex-col justify-end;
}
.mainHeading {
    @apply mt-0 md:mt-48 lg:mt-24 lg:pt-32 xl:pt-0 xl:mt-24 text-4xl md:text-6xl font-bold text-center;
}
.subHeading {
    @apply text-base md:text-lg text-gray-600 text-center px-2 md:p-8;
}

.itemClass{
    @apply px-1;
}


@keyframes darken {
    from {
        @apply opacity-100;
    }
    to {
        @apply opacity-90;
    }
}
