.automatisierte{
    @apply w-11/12 lg:w-10/12 md:w-10/12 rounded-3xl h-fit lg:py-20 lg:px-20 md:p-16 p-10 m-auto mb-20 bg-no-repeat bg-cover rounded-3xl;
}


.automatisierte h1{
    @apply text-4xl lg:text-6xl md:text-5xl font-bold text-primary-white mb-2 w-11/12 leading-8;
}

.automatisierte h3{
    @apply lg:text-4xl text-2xl text-primary-white font-bold mb-10;
}

.automatisierte p{
    @apply  font-bold text-primary-white leading-6 mb-10;
}

.automatisierteList{
    @apply w-full mb-10;
}

.automatisierteList div{
    @apply flex flex-row justify-start mb-2 items-center;
}

.automatisierteIcon{
    @apply text-lg bg-primary-white mr-2 p-0.5 self-center text-center text-primary-dark font-bold w-fit h-fit rounded-full ;
}

.automatisierteList div p{
    @apply self-center m-0;
}


.MoreAboutContainer {
    display: flex;
    flex-direction: column;
}

.heroSection {
    display: flex;
    flex-direction: column;
    background: radial-gradient( rgb(125, 5, 5) , rgb(23, 1, 1));
    color: #fafafa;
    font-size: 2rem;
    font-weight: bold;
    margin-top: 1rem;
    height: 100%;
    width: 100%;
    padding-top: 10px;
}

.MehrHeroSection {
    font-size: 5rem;
    margin: 0 auto;
    padding-top: 32px;
}

.KeinHeroSection {
    font-size: 5rem;
    margin-left:4rem;
}

.SiteHeroSection {
    font-size: 2.50rem;
    margin-left:4rem;
}

.companies {
    width: 100%;
    height: 20vh;
    background-color: red;
}

.video {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.leftSection {
    font-size: 4rem;
    font-weight: 700;
    margin: 2rem;
}

.novaHeroSectionInfo {
    font-size: 32px;
}

.IconBtnBtn {
    width: fit-content;
    display: flex;
    font-size: 1.4rem;
    border-radius: 50px;
    background-color: #000;
    color: #fff;
    align-items: center;
    justify-content: center;
    padding: 8px 16px;
    box-shadow: 0px 10px 15px 0px rgb(0 0 0 / 20%);;
    margin-top: 2rem;
}

.icon {
    margin-left: 15px;
}

.rightSection {
    visibility: visible;
}

.application {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.leftSide {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    width: 100%;
    height: 100%;
    border-radius: 25px;
    padding: 45px;
    box-sizing: border-box;
    background: #296c82;
    margin: 2rem;
    color: #fafafa;
}

.rightSide {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    width: 100%;
    height: 100%;
    border-radius: 25px;
    padding: 45px;
    box-sizing: border-box;
    background-color: #db3247;
    margin: 32px;
    color: #fafafa;
}

.risk {
    font-size: 45px;
    font-weight: 700;
    color: #fff;
}

.partnerText {
 font-size: 18px;
 font-weight: bold;
 margin: 1rem;
}


.AnimationList {
    display: flex;
    color: #fff;
    align-items: center;
    margin-left: 1rem;
    font-weight: bold;
}

.check {
    margin: 8px;
    font-size: 25px;
}

.makeBtn {
    padding: 12px 24px;
    color: #808080;
    border: 3px solid #fff;
    margin: 32px 0px 0px 25px;
    border-radius: 40px;
    font-weight: bold;
}

.makeBtn:hover {
    background: #fafafa;
}

.terms {
    display: flex;
    /* width: 100%;
    height: 100vh;
    padding: 30px;
  */
}

.wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    box-sizing: border-box;
    width: 100%;
    height: 100vh;
    border-radius: 25px;
    background: #005C81;
    color: #fafafa;
    margin: 0px 25px 0px 32px;
}

.long {
    flex: 3;
    padding: 50px;
    box-sizing: border-box;
}

.longTerms {
    font-size: 4rem;
    font-weight: 700;
    width: 90%;
    color: #fff;
}

.successStory {
    color: #fff;
    width: 92%;
    font-weight: bold;
    line-height: 1.5;
    font-size: 1.1rem;
    margin: 20px 0;
}

.moreApps {
    color: #fff;
    width: 92%;
    font-weight: bold;
    line-height: 1.5;
    padding-top: 32px;
    font-size: 1.1rem;
    margin: 20px 0;
}

.customersResponseContainer {
    margin: 80px 0px 0px 0px;
}

.customersResponse {
    border: 3px solid #fff;
    color: #fff;
    padding: 12px 24px;
    border-radius: 25px;
    font-size: 15px;
    font-weight: bold;
}

.customersResponse:hover {
    background-color: #fff;
    color: #000;
}

.why {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 4rem 16px 10px 16px;
}

.card {
    padding: 40px;
    border-width: 1px;
    border-color: #FFFFFF12;
    border-radius: 10px;
    width: 23%;
    height: 100%;
    box-shadow: 0px 10px 15px 0px rgb(0 0 0 / 15%); 
}

.SavingInfo {
    font-size: 18px;
    font-weight: bold;
    text-transform: capitalize;
}



@media (min-width: 320px) and (max-width: 1200px) {
.MoreAboutContainer {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.heroSection {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 0 auto;
    font-size: 1rem;
    word-wrap: break-word;
    flex-wrap: wrap;
}

.MehrHeroSection, 
.KeinHeroSection  {
    font-size: 2.70rem;
    text-align: left;
}

.KeinHeroSection, 
.SiteHeroSection {
    font-size: 28px;
    margin: 16px;
}

.video {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.leftSection {
    font-size: 3rem;
    font-weight: 700;
    margin: 2rem;
}

.novaHeroSectionInfo {
    font-size: 1rem;
}

.IconBtnBtn {
    display: flex;
    flex-direction: row;
    font-size: 1.4rem;
    border-radius: 50px;
    background-color: #000;
    color: #fff;
    align-items: center;
    justify-content: center;
    width: 100%;
    box-shadow: 0px 10px 15px 0px rgb(0 0 0 / 20%);;
    margin-top: 2rem;
}

.rightSection {
    display: none;
}

.application {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    overflow: hidden;
}

}

@media (min-width: 320px) and (max-width: 1200px) {
    .leftSide {
        align-items: center;
        text-align: center;
        margin: 0px 32px 20px 0px;
    }

    .rightSide {
        margin: 1rem 3px 20px 0px;
        align-items: center;
        text-align: center;
    }
    
    .wrapper {
        display: flex;
        flex-direction:column;
        box-sizing: border-box;
        width: 100%;
        border-radius: 25px;
        background: #005C81;
        color: #fafafa;
        margin: 0 auto;
    }

    .longTerms {
        font-size: 2rem;
        font-weight: 700;
        color: #fff;
    }

    .why {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }
}



@media (min-width: 320px) and (max-width: 1200px) {
    .card {
        padding: 40px;
        border-width: 1px;
        border-color: #FFFFFF12;
        border-radius: 10px;
        width: 100%;
        height: 100%;
        box-shadow: 0px 10px 15px 0px rgb(0 0 0 / 15%); 
    }
}



