.fullyAutomatedCardBlue {
  @apply flex flex-col justify-between  px-20 py-12 rounded-3xl bg-primary-teal h-screen;
  width: 45vw;
}
.fullyAutomatedCardRed {
  @apply flex flex-col justify-between  px-20 py-12 rounded-3xl text-primary-red h-screen;
  width: 45vw;
}

.fullyAutomatedCardTitle {
  @apply w-20;
}
.fullyAutomatedCardTitle h1 {
  @apply text-6xl text-primary-white font-bold;
}
.fullyAutomatedCardBody {
  @apply w-full;
}
.fullyAutomatedCardBody p {
  @apply text-primary-white font-bold self-start;
}

.fullyAutomatedCardBottom {
  @apply flex flex-col justify-between h-1/3;
}
.CardBottomList {
  @apply flex flex-row   text-primary-white mb-2 font-bold;
}
.bottomCardIcon {
  @apply h-6 w-6 rounded-full bg-primary-white mr-2 text-primary-green text-xl font-bold text-center flex justify-center;
}

.FullyAutomatedCardBottomButton {
  @apply border-primary-white border-2 rounded-3xl h-12 w-40 rounded-full text-primary-white hover:bg-primary-white hover:text-primary-dark font-semibold;
}
