.moduleLander {
    background: url(../../resources/images/road2.jpg) fixed;
    @apply bg-center bg-no-repeat bg-cover;
}
.innerLander {
    @apply w-full h-fit lg:h-[800px] px-10 lg:px-0 pb-60 lg:pb-10 mb-20;
    @apply bg-primary-teal bg-opacity-[0.85];
}
.moduleLanderWrapper {
    @apply w-full lg:w-full pt-32 lg:py-40 lg:pl-16 m-auto;
}
.moduleLanderWrapper h2 {
    @apply text-4xl w-3/4 xl:w-full lg:text-6xl mt-10 lg:mt-0 mb-5 lg:mb-0;
    @apply font-bold text-primary-white;
    @apply m-auto;
}

.moduleLanderWrapper p {
    @apply text-xl lg:text-xl font-normal text-primary-white w-full;
}

/*styles of the moduleNav*/

.moduleNav {
    @apply hidden lg:flex flex-row  w-full flex-wrap justify-center w-[100vw];
}

.moduleNav a {
    @apply text-lg text-primary-white/80 focus:text-primary-green hover:text-primary-green ml-3 font-semibold flex flex-row justify-center items-center;
}

.modulsubNav {
    @apply absolute z-20 top-28 h-10 w-56 px-5 py-1 justify-center items-start flex flex-col hover:text-primary-green text-lg font-semibold h-fit rounded-3xl bg-primary-teal/70;
}

.modulsubNav a {
    @apply self-start;
}

.moduleNavResp {
    @apply lg:hidden w-full pt-10;
}

.ModuleNavRespHamburger {
    @apply w-full flex justify-end text-4xl text-primary-white font-bold;
}

.moduleNavRespLinks {
    @apply w-full absolute z-40 p-5 mt-5 h-fit bg-primary-white left-0 rounded-3xl;
}

.moduleNavRespLinks a {
    @apply flex justify-between w-full mb-1 hover:text-primary-green focus:text-primary-green text-xl font-semibold;
}

.respLine {
    @apply w-full h-0.5 mb-1 bg-primary-grey/20;
}

.respsubNav {
    @apply w-11/12 absolute p-5 m-auto top-32 shadow-md bg-primary-white rounded-b-3xl h-fit flex flex-col justify-between;
}

.respsubNav a {
    @apply text-primary-dark hover:text-primary-dark focus:text-primary-dark;
}

.respsubNav2 {
    @apply w-11/12 absolute p-5 m-auto top-60 shadow-md bg-primary-white rounded-b-3xl h-fit flex flex-col justify-between;
}

.respsubNav2 a {
    @apply text-primary-dark hover:text-primary-dark focus:text-primary-dark;
}

.respClose {
    @apply text-primary-dark font-bold;
}

.modulNavArrow {
    @apply hover:border-2 hover:p-3 self-center border-primary-dark/70;
}
