.mainWrapper{
    @apply w-full flex mb-12;
    overflow-x: hidden;
}

.textWrapper{
    @apply flex w-full justify-start px-6 pt-12;
    @apply md:px-8 md:pt-40;
    @apply  lg:py-60;
}

.title{
    @apply w-full text-4xl font-bold;
    @apply lg:text-6xl;
}

.subtitle{
    @apply w-full text-2xl font-bold mt-5;
    @apply md:mt-10;
    @apply lg:text-4xl; 
}

.button{
    @apply flex px-4 text-white text-base bg-black  py-1 rounded-full font-semibold mt-4 shadow-md hover:bg-white hover:text-black;
    @apply md:px-6 font-normal md:text-base;
}

.imageWrapper{
    @apply hidden lg:w-1/2 lg:flex justify-end items-center;
}