.datenBanken{
    @apply w-full m-auto my-20;
}

.datenBanken h1{
    @apply text-4xl lg:text-7xl text-primary-dark font-bold text-center mb-10;
}

.datenBanken p{
    @apply text-xl text-primary-dark text-center;
}

.datenBankenMain{
    @apply flex flex-col lg:flex-row justify-center mt-10 items-center ;
}

.datenBankenChild{
    @apply w-96 flex flex-col h-40 justify-between items-center  lg:px-8 lg:mb-5 mb-10;
}
.datenBankenChild h3{
    @apply text-3xl font-bold text-primary-dark text-center;
}
.datenBankenChild p{
    @apply text-lg font-semibold text-center ;
}

.datenBankenChild a button{
    @apply w-fit px-5 py-1  bg-primary-teal text-primary-white font-semibold rounded-3xl;
}