.container {
    @apply p-8 w-11/12 lg:w-9/12 xl:w-7/12;
    @apply flex flex-col items-center;
    @apply m-auto ;
}

.mainHeading {
    @apply text-4xl md:text-7xl font-bold text-center;
}
.subHeading {
    @apply text-lg py-8 pb-12;
}

.sectionWrapper {
    @apply w-screen h-fit overflow-x-hidden;
}

.textDiv {
    @apply flex flex-col gap-4;
}
.text {
    @apply text-lg;
}
.link {
    @apply text-primary-teal hover:cursor-pointer underline-offset-1 underline;
}
.bottomWrapper {
    @apply m-auto pt-32 w-full;
}
.bottom {
    @apply flex flex-col md:flex-row gap-4 py-12;
}
.videoframe {
    @apply w-full md:w-1/2;
}
.video {
    @apply w-full h-full rounded-3xl;
}
.textDiv2 {
    @apply w-full md:w-1/2;
}
