.globalWrapper{
    @apply ml-5 mx-2 leading-none;
    @apply md:ml-8 md:mx-6;
}

.mainWrapper{
    @apply w-full rounded-3xl px-6 py-12 mt-6 text-white font-bold bg-gradient-to-r from-cyan-700 to-cyan-900;
    @apply md:flex md:px-10 md:py-20 md:mt-12;
}

.title{
    @apply text-3xl leading-none;
    @apply md:text-6xl md:px-10;
}

.paragraph{
    @apply mt-10 font-normal text-sm;
    @apply md:px-10 md:text-base md:font-semibold;
}

.button{
    @apply border-2 border-white py-1 px-3 text-white text-sm  font-normal rounded-3xl mt-12 bg-transparent hover:text-black hover:bg-white;
    @apply md:mx-10 md:text-base md:font-semibold;
}

.mobileWrapper{
    @apply flex w-full mt-10 justify-center items-center;
    @apply md:w-1/2 md:mt-0 md:relative
}

.mobile{
    @apply flex justify-center items-center relative;
}

.mobileSize{
    @apply scale-x-[1.15] h-[335px] absolute z-10;
    @apply md:scale-x-110 md:h-[505px]
}