.instructorSection{
    @apply w-full my-20;
}

.instructorSectionWrapper{
    @apply w-11/12 flex flex-col m-auto items-center justify-center lg:flex-row lg:px-32;
}
.moduleTab{
    @apply w-full lg:w-3/5 flex flex-col mb-10 self-center;
}

.moduleTab h3{
    @apply text-3xl mb-10 font-bold ;
}
.moduleNavLinks{
    @apply flex flex-col;
}
.moduleNavLinks a{
    @apply font-semibold mb-2 hover:text-primary-green w-fit;
}

.instructor{
    @apply w-full lg:w-3/5 flex flex-col items-center;
}

.instructor h3{
    @apply text-primary-dark text-3xl font-bold mb-10 text-center;
}

.instructor p{
    @apply text-xl  ;
}

.instructorPic{
    @apply w-64 h-3/4 rounded-3xl m-auto my-10 mb-5;
}

.instructorImage p{
    @apply text-center text-primary-dark;
}
.modulsubNav{
    @apply flex flex-col w-full justify-center ml-10;
}
.modulsubNav a{
    @apply mb-2 text-primary-dark hover:text-primary-green;
}

.instructorIcon{
    @apply inline-block ml-10 text-xl hover:border-2 hover:text-primary-green hover:border-primary-grey hover:p-3;
}