.forWrapper {
    @apply py-20 px-40;
}

.forTitle {
    @apply text-center text-4xl font-medium my-4;
}

.forText {
    @apply text-center text-sm w-2/3 mx-auto my-2;
}

/* Nkenfa your taiwind css here */

.forCounter {
    @apply h-8 w-10 bg-primary-green text-center rounded-xl text-white;
}

.forMainWrapper {
    @apply grid grid-cols-1  mt-16;
    @apply sm:grid-cols-1;
    @apply md:grid-cols-1;
    @apply lg:grid-cols-3;
    /* @apply md:grid-cols-1; */
}

.helpWrapper {
    @apply rounded-2xl flex flex-col justify-center items-center w-full px-9 py-7;
}

.helpIconWrapper {
    @apply w-full mb-8;
}

.liner{
    @apply flex flex-row justify-center ml-10;
}

.helpIcon {
    @apply text-7xl ;
    @apply mb-11;
}


.helpTitle {
    @apply text-lg font-bold text-center mt-1;
}

/* To apply button border */
.helpButtonPlace {
    @apply rounded-2xl

}

.buttonWrapper {
    @apply flex justify-evenly rounded-full items-center px-20 py-3;
    @apply sm:text-left  px-8;
    @apply md:text-left  px-8;
    @apply lg:px-32 text-left space-x-1;
    /* @apply md:text-left space-x-6 px-16; */
}

.buttonText {
    @apply font-bold ml-1.5;
}

