.learningShockCard{
    @apply flex flex-col   p-16  h-fit w-full bg-primary-white rounded-3xl mt-10;
}

.learningShockHeader{
    @apply w-3/4 mb-4 ;
}
.learningShockHeader h1{
    @apply text-4xl font-bold text-primary-dark ;
    @apply md:text-4xl;
    @apply lg:text-7xl;
}

.learningShockBody{
    @apply w-full mb-3;
}

.learningShockBody p{
    @apply w-full  text-xl text-left  ;
}