.mainWrapper{
    @apply py-24 w-full bg-gradient-to-b text-white from-cyan-700 to-cyan-900;
    @apply md:py-14 
}

.titleWrapper{
    @apply pt-2 px-4;
    @apply md:pl-[72px] md:px-6;
}

.title{
    @apply font-bold text-4xl ;
    @apply md:text-6xl;
}

.subtitle{
    @apply mt-12 font-bold text-2xl pl-4;
    @apply md:mt-[90px] md:text-4xl md:pl-20
}

