.cardWrapper {
    @apply w-full xl:w-96 h-44 p-6;
    @apply rounded-2xl bg-primary-white;
    @apply cursor-pointer;
    @apply flex flex-col justify-around;
}

.secondText,
.secondTextOut {
    @apply text-xl font-bold;
}
.textDiv {
    @apply pb-8;
}
.btn {
    @apply w-32 bg-primary-teal rounded-full py-1 text-primary-white;
}
/* For mouse entry */
.cardWrapperOut {
    @apply w-full xl:w-96 h-44  p-6;
    @apply rounded-2xl bg-[#5e676b];
    @apply backdrop-filter bg-opacity-75 backdrop-blur-lg;
    @apply flex flex-col justify-around;
}
.topTextOut,
.secondTextOut {
    @apply text-primary-white;
}

