.wrapper {
    @apply block lg:flex m-auto md:justify-around items-center h-fit;
}
.heading {
    @apply text-3xl py-0 md:py-4 font-bold text-gray-800;
}
.text{
    @apply text-gray-700 text-start ;
}
.rightDiv {
    @apply w-full  xl:w-6/12 h-3/4 ;
}
.rightDivContainer {
    @apply text-center w-1/2 m-auto flex flex-col gap-4 py-16;
}
.a {
    @apply hover:text-blue-500 px-1 underline;
}
.image {
    @apply rounded-2xl w-80 h-56 m-auto;
}
